<template>
  <div id="app">
  <div class="header" v-if="!/^\/login.*|^\/register.*/.test($route.fullPath)">
    <myHeader></myHeader>
  </div>
  <div class="main">
    <!-- <my-main></my-main> -->
   <keep-alive><router-view></router-view></keep-alive>
  </div>
  <div class="footer" v-if="!/^\/login.*|^\/register.*/.test($route.fullPath)">
    <my-footer></my-footer>
  </div>
  </div>
</template>

<script>
import myHeader from '@/components/header/index'
import myMain from '@/components/index/index'
import myFooter from '@/components/footer/index'
export default {
  name: 'App',
  components: {
    myHeader,
    myMain,
    myFooter
  }
}
</script >

<style scoped>
  #app {
    min-width: 1250px;
  }
</style>



