<template>
  <el-row class="top_bar">
    <el-col :span="6">
      <city></city>
    </el-col>
    <el-col :span="5">
      <user></user>
    </el-col>
    <el-col :span="5" class="empty">
      aa
    </el-col>
    <el-col :span="8">
      <my-nav></my-nav>
    </el-col>
  </el-row>
</template>
<script>
import city from './city'
import user from './user'
import myNav from './nav'
export default {
  components: {
    city,
    user,
    myNav
  }
}
</script>
<style scoped>
  .top_bar {
    position: relative;
    z-index: 10;
    padding: 0 5%;
    height: 50px;
    line-height: 50px;
    min-width: 1250px;
    background-color: #f8f8f8;
  }
  .empty {
    font-size: 0px;
  }
</style>



