<template>
  <el-row class="mian">
    <el-col :span="5" class="menu">
      <my-menu></my-menu>
    </el-col>
    <el-col :span="19" class="main_content">
      <my-main></my-main>
    </el-col>
    <el-col :span="24">
      <my-list></my-list>
    </el-col>
    </el-row>
</template>
<script>
import myMenu from './menu'
import myMain from './main'
import myList from './list'
export default {
  components: {
    myMenu,
    myMain,
    myList
  }
}
</script>
<style scoped>
.mian {
  padding: 0px;
  margin: 0 5%;
  /* background-color: rgba(0, 0, 0, 0.1); */
}
.menu {
  /* display: inline-block; */
  box-sizing: border-box;
  /* margin-left: 5%; */
}
</style>



