<template>
  <div class="list">
    <h2 class="title">{{list.title}}</h2>
    <span class="list_item" :key="indx" v-for="(item, indx) in list.content">{{item}}</span>
  </div>
</template>
<script>
export default {
  props: {
    list: {
      type: Object,
      default: () => {}
    }
  }
}
</script>
<style scoped>
  .list {
    position: fixed;
    display: none;
    top: 0px;
    left: 0px;
    z-index: 10;
    padding: 15px;
    max-width: 400px;
    border: 1px solid rgba(102, 102, 102, 0.3);
    border-radius: 4px;
    font-size: 0px;
    background-color: #fff;
  }

  .list::before {
    content: '';
    position: absolute;
    z-index: -1;
    left: 20px;
    top: -6px;
    width: 10px;
    height: 10px;
    transform: rotate(45deg);
    background: #FFF;
    border-left: 1px solid rgba(102, 102, 102, 0.3);
    border-top: 1px solid rgba(102, 102, 102, 0.3);
  }

  .title {
    text-align: left;
    font-size: 16px;
    color: rgba(102, 102, 102, 0.2);
  }

  .list_item {
    display: inline-block;
    padding: 10px 0px;
    width: 25%;
    white-space: nowrap;
    font-size: 16px;
    text-overflow: ellipsis;
    overflow: hidden;
  }
</style>



