<template>
  <el-row class="com_footer_container">
    <el-col :span="24">
      <div class="list_wrapper">
        <ul class="list" >
          <li class="list_item" :key="item" v-for="item in server">
            <router-link class="link" to="/">{{item}}</router-link>
          </li>
        </ul>
        <ul class="list" >
            <li class="list_item" :key="item" v-for="item in hotel">
              <router-link class="link" to="/">{{item}}</router-link>
            </li>
          </ul>
      </div>
      <div class="list_wrapper">
          <ul class="list" >
            <li class="list_item" :key="item" v-for="item in cate">
              <router-link class="link" to="/">{{item}}</router-link>
            </li>
          </ul>
      </div>
      <div class="list_wrapper">
          <ul class="list" >
            <li class="list_item" :key="item" v-for="item in proxy">
              <router-link class="link" to="/">{{item}}</router-link>
            </li>
          </ul>
          <ul class="list" >
            <li class="list_item" :key="item" v-for="item in rules">
              <router-link class="link" to="/">{{item}}</router-link>
            </li>
          </ul>
      </div>
      <div class="list_wrapper">
          <ul class="list" >
            <li class="list_item" :key="item" v-for="item in attention">
              <router-link class="link" to="/">{{item}}</router-link>
            </li>
          </ul>
          <ul class="list" >
            <li class="list_item" :key="item" v-for="item in about">
              <router-link class="link" to="/">{{item}}</router-link>
            </li>
          </ul>
          <ul class="list" >
            <li class="list_item" :key="item" v-for="item in report">
              <router-link class="link" to="/">{{item}}</router-link>
            </li>
          </ul>
      </div>
      <div class="list_wrapper last_list_wrapper">
          <ul class="list" >
            <li class="list_item" :key="item" v-for="item in cost">
              <router-link class="link" to="/">{{item}}</router-link>
            </li>
          </ul>
          <ul class="list" >
            <li class="list_item" :key="item" v-for="item in funny">
              <router-link class="link" to="/">{{item}}</router-link>
            </li>
          </ul>
          <ul class="list" >
            <li class="list_item" :key="item" v-for="item in complaint">
              <router-link class="link" to="/">{{item}}</router-link>
            </li>
          </ul>
          <ul class="list" >
              <li class="list_item" :key="item" v-for="item in entry">
                <router-link class="link" to="/">{{item}}</router-link>
              </li>
            </ul>
        </div>
    </el-col>
    <el-col :span="12">
      <ul class="license_wrapper">
        <li> ©美团网团购 meituan.com京ICP证070791号京ICP备10211739号</li>
        <li>广播电视节目制作经营许可证（京）字第03889号</li>
        <li>食品经营许可证互联网药品信息服务资格证</li>
        <li>医疗器械网络交易服务第三方平台备案：（京）网械平台备字[2018]第00004号</li>
      </ul>
    </el-col>
    <el-col :span="12" class="license_wrapper">
        <img :src="license" alt="" height="50px"><span class="license_text">北京三快科技有限公司</span>
        <img :src="bgImg" alt="">
    </el-col>
  </el-row>
</template>
<script>
import {server, hotel, cate, proxy, rules, attention, about, report, cost, funny, complaint, entry} from '@/public/js/comFooter'
import license from './1.png'
import bgImg from './2.jpg'
export default {
  data () {
    return {
      server,
      hotel,
      cate,
      proxy,
      rules,
      attention,
      about,
      report,
      cost,
      funny,
      complaint,
      entry,
      license,
      bgImg
    }
  }
}
</script>
<style scoped>
  .com_footer_container {
    margin: 0 50px;
    border-top: 1px solid rgba(153, 153, 153, 0.3);
    border-bottom: 1px solid rgba(153, 153, 153, 0.3);
    text-align: center;
  }
  .link {
    display: inline-block;
    padding: 8px;
  }
  .list_wrapper {
    display: inline-block;
    vertical-align: top;
    text-align: left;
  }
  .list {
    margin: 40px 40px 0px 0px;
  }
  .last_list_wrapper {
    margin-right: 0px;
  }
  .license_text {
    display: inline-block;
    vertical-align: top;
    margin-top: 14px;
    margin-left: 10px;
  }
  .license_wrapper {
    margin-top: 30px;
  }
  .license_wrapper li {
    padding: 5px;
    color: #999;
    text-align: left;
  }
</style>



