<template>
  <el-row>
    <el-col :span="24">
      <div class="recom_container">
        <div class="recom_warpper">
          <div class="img_wrapper" :style="{height: imgHeight}">
            <img :src="imgUrl" alt="" width="100%" >
          </div>
          <div class="slot">
            <slot>这是一个默认的slot</slot>
          </div>
        </div>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import img1 from './1.jpg'
export default {
  props: {
    imgUrl: {
      type: String,
      default: img1
    },
    imgHeight: {
      type: String,
      default: '200px'
    }
  },
  data () {
    return {
      img1
    }
  }
}
</script>
<style scoped>
  .recom_warpper {
    /* margin: 10px 0 0 10px; */
    margin-top: 10px;
    padding: 5px;
  }

  .recom_warpper:hover {
    background-color: rgba(172, 166, 166, 0.1);
  }

  .img_wrapper {
    padding: 10px;
    overflow: hidden;
  }
</style>



