<template>
  <el-row>
    <el-col :span="24" class="food-container">
      <div class="avatar_warpper">
        <img :src="defaultUrl" alt="">
      </div>
      <div class="f_content">
        <h5 class="f_title">
          团购套餐{{num}},建议2人食用。
        </h5>
        <div class="sale_count">
          <span>已售{{Math.floor(Math.random()*1000)}}</span>
        </div>
        <div class="price_wrapper">
          <span>￥<span class="price">{{price}}</span></span>
          <span class="old_price">门店价￥{{price - 10}}</span>
        </div>
      </div>
      <div class="snapUp" @click="buyProduct">立即抢购</div>
    </el-col>
  </el-row>
</template>
<script>
export default {
  props: {
    num: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {
      price: Math.floor(Math.random() * 1000 + 30),
      defaultUrl: require('./food.jpg')
    }
  },
  methods: {
    buyProduct () {
      const food = {
        price: this.price,
        name: `团购套餐${this.num},建议2人食用。`
      }
      this.$emit('buy-product', food)
    }
  }
}
</script>
<style scoped>
.food-container {
  width: 100%;
}
.avatar_warpper {
  display: inline-block;
  width: 100px;
  height: 100px;
}
.avatar_warpper>img {
  width: 100%;
  /* height: 100%; */
}
span {
  line-height: 40px;
  font-size: 15px;
  color: #666;
}
.f_content {
  display: inline-block;
  margin-left: 30px;
}
.f_title {
  font-size: 16px;
}
.price {
  font-size: 36px;
  color:rgb(141, 114, 24);
}
.old_price {
  font-size: #666;
}
.snapUp {
  position: absolute;
  right: 50px;
  top: 50%;
  transform: translateY(-50%);
  width: 100px;
  height: 50px;
  border-radius: 10px;
  background: orange;
  text-align: center;
  font-size: 20px;
  line-height: 50px;
  color: #fff;
  font-weight: 500;
}
</style>



