<template>
  <el-row>
    <el-col :span="24">
      <titlebar :showMore="true" :listTitle="titlebarInfo.listTitle" :bgColor="titlebarInfo.bgColor" :color="titlebarInfo.color"
        :list="titlebarInfo.infoTitle"></titlebar>
    </el-col>
    <el-col :span="24">
      <div class="block">
        <el-carousel trigger="click"  height="280px" :autoplay="false" :loop="false">
          <el-carousel-item :key="indx" v-for="(item, indx) in listData">
              <div class="movies" :key="indx" v-for="(item, indx) in item">
                  <my-img :imgUrl="item.imgUrl" imgHeight="280px">
                    <div class="img_wrapper">
                      <span class="ver_type" v-show="item.ver">{{item.ver}}</span>
                      <h5 class="review" v-if="!item.preSale">
                        <span class="text">观众评</span><span class="rank">{{item.mk}}</span>分
                      </h5>
                      <h5 class="review" v-else>
                        <span class="rank">{{item.wish}}</span><span class="want">人想看</span>
                      </h5>
                      <div class="name_pay">
                        <h5 class="name">{{item.nm}}</h5>
                        <span class="pay">
                          {{item.preSale ? '预售' : '购票'}}
                        </span>
                      </div>
                    </div>
                  </my-img>
                </div>
          </el-carousel-item>
        </el-carousel>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import titlebar from './titlebar'
import { getFilms } from '@/public/js/getData'
import myImg from './img'
export default {
  components: {
    titlebar,
    myImg
  },
  data () {
    return {
      titlebarInfo: {
        listTitle: '猫眼电影',
        color: '#fff',
        bgColor: '#fa3c68',
        infoTitle: ['正在上映', '即将上映']
      },
      listData: []
    }
  },
  methods: {
    _initDiscount () {
      const ci = this.$store.state.geo.ci
      getFilms(ci).then(res => {
        this._formatDataList(res.data.data.hot)
      })
    },
    _formatDataList (infos) {
      const tmpArr = []
      infos.map(item => {
        const { nm, mk, ver, preSale, wish, img } = item
        tmpArr.push({
          nm,
          mk,
          ver: ver.split(' ')[0].replace(/2D\/|2D/, ''),
          preSale,
          wish,
          imgUrl: img.replace('/w.h', '')
        })
      })
      this.listData.push(tmpArr, tmpArr.splice(0, 5))
    }
  },
  created () {
    this._initDiscount()
  }
}
</script>
<style scoped>
  .movies {
    position: relative;
    display: inline-block;
    width: 20%;
  }

  .ver_type {
    position: absolute;
    top: 35px;
    /* left: -10px; */
    padding: 5px;
    background-color: #17a6ff;
    color: #fff;
  }

  .review {
    position: absolute;
    left: 25px;
    bottom: 65px;
    color: #fff;
  }

  .text {
    margin-right: 10px;
    font-weight: 500;
  }

  .rank {
    vertical-align: middle;
    font-size: 16px;
    font-weight: 500;
    color: #FD9827;
  }

  .want {
    vertical-align: middle;
  }

  .name_pay {
    position: absolute;
    left: 25px;
    bottom: 50px;
    width: 100%;
    font-size: 16px;
    color: #fff;
  }

  .pay {
    position: absolute;
    right: 50px;
    bottom: 0px;
    padding: 8px 12px;
    border-radius: 12px;
    background-color: #FF4949;
    color: #fff;
    cursor: pointer;
  }
</style>



