<template>
  <div :id="id" :style="{width: width, height: height, margin: '0px auto'}">

  </div>
</template>
<script>
export default {
  props: {
    height: {
      type: String,
      default: '200px'
    },
    width: {
      type: String,
      default: '80%'
    },
    mapText: {
      type: Array,
      default: () => []
    },
    store: {
      type: Object,
      default: () => { }
    }
  },
  data () {
    return {
      id: `map_${Math.random().toString(16).slice(3, 10)}`,
      map: '',
      info: []
    }
  },
  methods: {
    _setCenter (location) {
      this.map.setCenter(location)
    }
  },
  mounted () {
    if (window.AMap) {
      const location = this.store.location
      /*  eslint-disable no-undef */
      this.map = new AMap.Map(this.id, {
        zoom: 11,
        center: location
      })
      AMap.plugin([
        'AMap.Scale'
      ], () => {
        this.map.addControl(new AMap.Scale())
      })
      this.$watch('store', (store) => {
        console.log(this.store)
        this.map.setZoom(10)
        const location = this.store.location
        this._setCenter(location)
        if (this.info.length) {
          this.map.remove(this.info)
          this.info.splice(0, 1)
        }
        const content = `<div style="background: #fff; position: relative;" >
          <p style="height: 30px;line-height: 30px;color: orange;">${this.store.name}</p>  
          <p style="height: 30px;line-height: 30px;">${this.store.address}</p>  
          <p style="height: 30px;line-height: 30px;"><span>电话：</span> ${this.store.tel}</p>  
          <p style="height: 30px;line-height: 30px;"><span>价格：</span>${this.store.cost}</p>  
          <p style="position: absolute; height: 30px; width: 30px;background: #fff;top: -20px; right: 0px; z-index: 100;"></p>
        </div>`
        const infoWindow = new AMap.InfoWindow({
          content,
          offset: new AMap.Pixel(0, -25)
        })
        this.info.push(new AMap.Marker({
          position: location
        }))
        this.map.add(this.info)
        infoWindow.open(this.map, location)
        setTimeout(() => {
          this.map.zoomIn()
        }, 3000)
      })
    }
  }
}
</script>
<style scoped>
</style>



