<template>
  <el-row class="container">
    <el-tabs :tab-position="tabPosition">
      <el-tab-pane label="我的美团"></el-tab-pane>
      <el-tab-pane label="订单">
        <el-tabs type="border-card">
          <el-tab-pane label="全部订单">
            <el-table class="order_container" :data="orderList">
              <el-table-column prop="orderNo" label="单号" width="180">
              </el-table-column>
              <el-table-column prop="name" label="商品名">
              </el-table-column>
              <el-table-column prop="name" label="商品名">
                <template>
                  <img :src="defaultUrl" alt="" height="100">
                </template>
              </el-table-column>
              <el-table-column prop="price" width="150" label="价格">
              </el-table-column>
              <el-table-column prop="count" label="数量">
              </el-table-column>
              <el-table-column label="总价">
                <template slot-scope="scope">
                  <span class="total">{{scope.row.count * scope.row.price}}</span>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="待付款">待付款</el-tab-pane>
          <el-tab-pane label="待使用">待使用</el-tab-pane>
          <el-tab-pane label="待评价">待评价</el-tab-pane>
        </el-tabs>
      </el-tab-pane>
      <el-tab-pane label="我的收藏">我的收藏</el-tab-pane>
      <el-tab-pane label="收藏的商家">收藏的商家</el-tab-pane>
      <el-tab-pane label="收藏的团购">收藏的团购</el-tab-pane>
      <el-tab-pane label="抵用券">抵用券</el-tab-pane>
      <el-tab-pane label="可用券">可用券</el-tab-pane>
      <el-tab-pane label="失效券">失效券</el-tab-pane>
      <el-tab-pane label="个人信息">个人信息</el-tab-pane>
      <el-tab-pane label="账户设置">账户设置</el-tab-pane>
    </el-tabs>
  </el-row>
</template>
<script>
export default {
  data () {
    return {
      order: ['全部订单', '待付款', '待使用', '待评价'],
      orderList: [],
      defaultUrl: require('./default.jpg'),
      tabPosition: 'left'
    }
  },
  computed: {
    total () {
      return this.orderList.reduce((pre, cur) => {
        pre.total = pre.total + cur.total
        return pre
      }, { total: 0 }).total
    }
  },
  activated () {
    this.$http.get('/api/order/orderList', {
      params: {
        user: this.$route.params.user
      }
    }).then((response) => {
      const { status, data: { code, res } } = response
      if (status !== 200 || code !== 0) return
      this.orderList = res
      console.log(this.orderList)
    })
  }
}
</script>
<style scoped>
  .container {
    width: 80%;
    margin: 30px auto;
  }

  .tabs {
    text-align: left;
  }
</style>



