import axios from 'axios'
const state = {
  currentGeo: {},
  curProvince: '',
  nearCitys: [],
  ci: 1,
  letter: 'bj',
  recentlyVisit: [],
  search: ''
}
const getters = {

}
const mutations = {
  setCurrentGeo (state, { city }) {
    state.currentGeo = city
  },
  setProvince (state, { province }) {
    state.curProvince = province
  },
  setNearCitys (state, { nearCitys }) {
    state.nearCitys = nearCitys
  },
  toggleGeo (state, {city}) {
    state.nearCitys.unshift(state.currentGeo)
    const indx = state.nearCitys.findIndex(itemCity => itemCity === city)
    state.nearCitys.splice(indx, 1)
    state.currentGeo = city
  },
  setSearch (state, {search}) {
    state.search = search
  }
}
const actions = {
  async setGeo ({commit}) {
    const res = await axios.get('http://cp-tools.cn/geo/getposition')
    let {province: p, city: c} = res.data
    const {city, province, nearCitys, code} = await _getInfos(p, c)
    if (code !== 0) return
    _commit(commit, city, province, nearCitys)
  },
  async toggleGeo ({commit}, {c, p}) {
    let zhixia = [
      {
        'id': '110000',
        'name': '北京市'
      },
      {
        'id': '310000',
        'name': '上海市'
      },
      {
        'id': '500000',
        'name': '重庆市'
      },
      {
        'id': '120000',
        'name': '天津市'
      }]
    const pIndx = zhixia.findIndex(city => city.name === c)
    if (pIndx >= 0) {
      let city = zhixia[pIndx]
      let province = city.province
      zhixia.splice(pIndx, 1)
      _commit(commit, city, province, zhixia)
      return
    }
    const {city, province, nearCitys, code} = await _getInfos(p, c)
    if (code !== 0) return
    _commit(commit, city, province, nearCitys)
  }
}

function _commit (commit, city, province, nearCitys) {
  commit({
    type: 'setCurrentGeo',
    city
  })
  commit({
    type: 'setProvince',
    province
  })
  commit({
    type: 'setNearCitys',
    nearCitys
  })
}
async function _getInfos (province, city) {
  const cityByProvince = await axios.get('http://cp-tools.cn/geo/province')
  const {province: allProvince} = cityByProvince.data
  const [{id: pid}] = allProvince.filter(provinceItem => provinceItem.name === province)
  const nearCitysRes = await axios.get(`http://cp-tools.cn/geo/province/${pid}`)
  let {code, city: nearCitys} = nearCitysRes.data
  city = nearCitys.filter(cityItem => cityItem.name === city)[0]
  nearCitys = nearCitys.filter(cityItem => cityItem.name !== city)
  return {
    code,
    province,
    city,
    nearCitys
  }
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



