<template>
  <el-row>
    <el-col :span="24" >
      <div class="title_bar_container" :style="{'background-color': bgColor, color: color}">
        <div class="title_warpper">
          <h3 class="list_title">
            {{listTitle}}
          </h3>
          <ul class="list" v-if="list.length">
            <li
              class="list_item"
              :class="{hover: activeIndx === indx}"
              :key="indx"
              @mouseenter="toggeleActiveIndx(indx)"
              v-for="(item, indx) in list">
             <span class="text">{{item.text}}</span>
            </li>
          </ul>
          <span class="more" v-if="showMore">
            更多<i class="iconfont iconicon-arrow_down"></i>
          </span>
        </div>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import '@/public/fonts/icon.css'
export default {
  props: {
    listTitle: {
      type: String,
      default: '默认标题'
    },
    list: {
      type: Array,
      default: () => []
    },
    color: {
      type: String
    },
    bgColor: {
      type: String
    },
    showMore: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      activeIndx: 0
    }
  },
  methods: {
    toggeleActiveIndx (indx) {
      if (this.activeIndx === indx) return
      this.activeIndx = indx
      this.$emit('toggleInfo', indx)
    }
  }
}
</script>
<style scoped>
  .title_bar_container {
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
  }
  .title_warpper {
    margin-top: 50px;
    padding: 0px 15px;
    height: 50px;
    line-height: 50px;
  }
  .title_warpper>* {
    cursor: pointer;
  }
  .list_title {
    display: inline-block;
    font-size: 14px;
    font-family: MFShangHei-Regular!important;
  }
  .list {
    display: inline-block;
  }
  .list_item {
    position: relative;
    display: inline-block;
  }
  .list_item.hover:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    border: 7px solid #fff;
    border-top-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
  }
  .text {
    padding: 0 15px;
  }
  .more {
    float: right;
    vertical-align: middle;
  }
</style>



