<template>
  <el-row class="login_container">
    <el-col :span="12">
      <div class="logo_container">
        <img :src="meituan" alt="" class="meituan" width="100px">
        <img :src="runman" alt="" width="80%">
      </div>
    </el-col>
    <el-col :span="12" class="right">
      <div class="login_form_container">
        <div class="login_type_wrapper">
            <span class="login_type">账号登录</span>
            <a class="login_modile" href="javascript:;">
              <span class="modile_text">手机动态密码登录</span><img class="phone" :src="phone" alt="" height="20px">
            </a>
        </div>
        <el-form
        ref="ruleForm"
        :model="ruleForm"
        class="form_wrapper">
          <el-form-item prop="mobile">
            <el-input type="text"
            v-model="ruleForm.mobile"
            class="mobile"
            autocomplete="off">
            </el-input>
          </el-form-item>
          <el-form-item prop="pasword">
            <el-input type="password"
            v-model="ruleForm.pasword"
            class="pasword"
            autocomplete="off">
            </el-input>
          </el-form-item>
          <a href="javascript:;" class="forget_pswrod">
            忘记密码
          </a>
          <el-form-item>
            <el-button type="primary"
            class="login_btn"
            @click="login">
            登录
          </el-button>
          <p class="singup_guide">
            还没有账号？<router-link to="/register"> 免费注册</router-link>
          </p>
          <div class="partner">
            <div class="partner_title">用合作网站账号登录</div>
            <div class="QQ_wrapper"></div>
            <div class="blog"></div>
          </div>
          </el-form-item>
        </el-form>
      </div>
    </el-col>
    <el-col :span="24">
      <div class="longin_footer">
        <div class="list">
          <a class="list_item" href="javascript:;">关于美团</a>
          <a class="list_item" href="javascript:;">加入我们</a>
          <a class="list_item" href="javascript:;">商家入驻</a>
          <a class="list_item" href="javascript:;">帮助中心</a>
          <a class="list_item" href="javascript:;">美团手机版</a>
        </div>
      </div>
      <div class="footer_text">
          ©2019 美团网团购 meituan.com 京ICP证070791号 京公网安备11010502025545号
      </div>
    </el-col>
  </el-row>
</template>
<script>
import runman from './runman.jpg'
import meituan from './meituan1.png'
import phone from './phone.png'
export default {
  data () {
    return {
      runman,
      meituan,
      phone,
      ruleForm: {
        mobile: '',
        pasword: ''
      }
    }
  },
  methods: {
    async login () {
      const res = await this.$http.post('api/users/login', this.ruleForm)
      !res.data.code ? this.$router.push('/') : alert(res.data.msg)
    }
  }
}
</script>
<style scoped>
  .login_container {
    position: absolute;
    min-width: 1250px;
    z-index: 10;
    left: 0px;
    top: 0px;
    width: 100%;
    padding: 100px 10% 0px 10%;
    box-sizing: border-box;
    background-color: #fff;
  }
  .meituan {
    display: block;
    margin-left: 50px;
  }
  .right {
    text-align: center;
  }
  .login_form_container {
    display: inline-block;
    text-align: left;
    width: 50%;
  }
  .logo_container {
    /* margin-top:50px; */
    text-align: center;
  }
  .login_type_wrapper {
    position: relative;
    margin-top: 50px;
    color: #999;
  }
  .login_type {
    padding: 8px;
  }
  .login_modile {
    float: right;
    vertical-align: top;
  }
  .modile_text {
    vertical-align: top;
  }
  .phone {
    margin: -4px 3px 0px 10px;
  }
  .mobile, .pasword {
    margin-top: 20px;
  }
  .forget_pswrod {
    float: right;
    padding: 8px;
  }
  .login_btn {
    display: block;
    width: 100%;
    margin-top: 20px;
    padding: 15px 0px;
    background-color: #2db3a6;
    border-color: #008177;
    cursor: pointer;
  }
  .login_btn:hover {
    background-color: #2eb7aa;
  }
  .partner {
    position: relative;
    margin-top: 20px;
    text-align: center;
    border-top: 1px solid rgba(153, 153, 153, 0.3);
  }
  .partner_title {
    position: absolute;
    top: -6px;
    left: 50%;
    padding: 0 10px;
    transform: translateX(-50%);
    font-size: 12px;
    line-height: 12px;
    background-color: #fff;
  }
  .QQ_wrapper, .blog{
    display: inline-block;
    margin: 40px 10px 40px 0px;
    height: 20px;
    width: 20px;
    background-size: 50px 50px;
  }
  .QQ_wrapper {
    background: url('./QQ.jpg') center center;
  }
  .blog {
    background: url('./weibo.jpg') center center;
    margin-right: 0px;
  }
  .list {
    padding: 15px 0px;
    border-top: 1px solid rgba(153, 153, 153, 0.2);
    border-bottom: 1px solid rgba(153, 153, 153, 0.2);
  }
  .list_item {
    display: inline-block;
    padding: 4px 20px 4px 20px;
    border-right: 1px solid rgba(153, 153, 153, 0.5);
  }
  .list_item:last-child {
    border-right: none;
  }
  .footer_text {
    color: #999;
    text-align: center;
    height: 50px;
    line-height: 50px;
  }
</style>



