<template>
  <el-row class="detail_container">
    <el-col :span="24">
      <crumb
        class="crumb"
        :city="$store.state.geo.currentGeo.name"
        :path="$store.state.geo.search"
        :type="$route.query.type"
        :product="$route.query.keyword">
      </crumb>
    </el-col>
    <el-col :span="24" class="product_wrapper">
      <el-col :span="16">
        <div class="p_content" v-show="product != null">
          <h5 class="p_name">
            {{product.name}}
          </h5>
          <div class="rate_wrapper">
            <el-rate
              class="star"
              v-model="product.rating"
              disabled
              text-color="#666">
            </el-rate>
            <span class="score">{{product.rating}}分</span>
            <span class="prePrice">人均￥{{product.cost}}</span>
          </div>
          <div class="p_infos">
            <p>
              <span>地址：</span> <span>{{product.address}}<i @click="toggleDialong" v-show="iconShow" class="icon iconfont iconmap"></i></span>
            </p>
            <p>
              <span>电话：</span><span>{{product.tel}}</span>
            </p>
            <p>
              <span>营业时间：</span><span>周一至周日 10：00-7：00</span>
            </p>
          </div>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="p_img_wrapper">
          <div class="big_img block">
            <el-carousel height="150px" :loop="false">
              <el-carousel-item v-for="(item, indx) in product.photos" :key="indx">
                <img :src="item.url" alt="">
              </el-carousel-item>
            </el-carousel>
          </div>
          <div class="shuffing">
            <div class="sm_img_box" :key="indx" v-for="(img, indx) in product.photos">
              <img :src="img.url" alt="">
            </div>
          </div>
        </div>
      </el-col>
    </el-col>
    <el-col :span="24">
      <h5 class="m_title">商家团购及优惠</h5>
    </el-col>
    <el-col :span="24" class="food_item">
      <food class="food" @buy-product="buyProduct" :key="indx" :num="num" v-for="(num, indx) in Math.floor(Math.random() *3 ) + 2"></food>
    </el-col>
    <el-col :span="24">
      <div class="big_map_dialong" v-show="isShowMapDialong">
        <div class="big_map_wrapper">
          <div class="del_btn" @click="toggleDialong">X</div>
          <big-map :store="product" class="big_map" height="100%" width="100%"></big-map>
        </div>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import crumb from '@/base/crumb/crumb'
import food from '@/components/detail/food'
import bigMap from '@/base/map/bigMap'
import '@/public/fonts/icon.css'
export default {
  components: {
    crumb,
    food,
    bigMap
  },
  data () {
    return {
      default: require('./default.jpg'),
      product: {},
      rand: Math.floor(Math.random() * 5) + 2,
      isShowMapDialong: false
    }
  },
  activated () {
    this._getProductByKeyword()
  },
  computed: {
    iconShow () {
      return Object.keys(this.product).length > 0 && this.product.location !== undefined && this.product.location.length
    }
  },
  methods: {
    async _getProductByKeyword () {
      const {keyword, type, city} = this.$route.query
      const {status, data: {code, product}} = await this.$http.get('http://cp-tools.cn/search/products', {
        params: {
          city,
          type,
          keyword
        }
      })
      if (!/2\d{2}/.test(status) || code !== 0) return
      const {name, biz_ext: {rating, cost}, address, tel, location, photos} = product
      this.product = {
        name,
        rating: Number(rating),
        cost: Number(cost),
        address: address.length ? address : '',
        tel: tel.length ? tel : '',
        location: location.split(',') || [],
        photos: photos.length > 0 ? photos : [{title: [], url: this.default}]
      }
    },
    toggleDialong () {
      this.isShowMapDialong = !this.isShowMapDialong
    },
    async buyProduct (food) {
      const {price, name} = food
      const {status, data: {code, res}} = await this.$http.post('/api/order/createOrder', {
        name,
        count: 1,
        price
      })
      if (status !== 200 || code !== 0) {
        return
      }
      this.$router.push('/order/' + res)
    }
  },
  mounted () {
  }
}
</script>
<style scoped>
  .detail_container {
    padding: 20px 50px 20px 50px;
    background: linear-gradient(to bottom right, rgba(102, 102, 102, 0.1), #fff);
  }
  .crumb {
    margin: 10px;
    color: rgb(218, 216, 216);
  }
  .product_wrapper {
    padding: 15px;
    /* border: 1px solid #666; */
    box-shadow: 0 0 10px rgba(102, 102, 102, 0.5);
  }
  .p_content {
    padding-right: 25px;
  }
  .p_name {
    font-size: 25px;
    color: #666;
    font-weight: 800px;
  }
  .rate_wrapper {
    padding: 15px 0px;
    border-bottom: 1px solid rgba(102, 102, 102, 0.5);
  }
  .star {
    display: inline-block;
  }
  .p_infos {
    padding: 15px 0px;
    font-size: 14px;
    line-height: 25px;
    color: #666;
    border-bottom: 1px solid rgba(102, 102, 102, 0.5);
  }
  .p_infos>p {
    cursor: pointer;
  }
  .p_infos>p:hover, .icon {
    color: #00c9b3;
  }
  .icon {
    margin-left: 15px;
  }
  .big_img {
    width: 100%;
    height: 150px;
    overflow: hidden;
  }
  .big_img > img {
    width: 100%;
    height: 100%;
  }
  .shuffing {
    font-size: 0px;
    width: 100%;
    overflow: hidden;
  }
  .sm_img_box {
    display: inline-block;
    width: 25%;
    height: 50px;
    padding:10px 3px 0px 0px;
    box-sizing: border-box;
  }
  .sm_img_box > img {
    width: 100%;
    height: 100%;
  }
  .m_title {
    padding-top: 15px;
    font-size: 25px;
    color: #666;
    line-height: 50px;
    height: 50px;
  }
  .food_item {
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0 0 10px #666;
    /* border: 1px solid rgba(102, 102, 102, 0.5); */
  }
  .food {
    padding: 20px 0;
    border-bottom: 1px solid rgba(167, 165, 165, 0.5);
  }
  .food:last-child {
    padding-bottom: 0px;
    border-bottom: none;
  }
  .big_map_dialong {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 100;
  }

  .big_map_wrapper {
    position: absolute;
    left: 50%;
    bottom: 0px;
    transform: translateX(-50%);
    padding: 50px 10px 0px 10px;
    width: 80%;
    height: 80%;
    background-color: rgb(68, 67, 67);
  }
  .del_btn {
    position: absolute;
    top: 0px;
    right: 15px;
    height: 50px;
    line-height: 50px;
    font-size: 36px;
    color: #fff;
    font-weight: 400;
    cursor: pointer;
  }
  .big_map {
    left: 50%;
    bottom: 0px;
    transform: translateX(-50%);
    box-shadow: 0 0 10px black;
  }
</style>



