import axios from 'axios'
export const getHotel = function (ci) {
  const params = {
    theme: 'quality',
    tab: 'all',
    ci,
    limit: 12
  }
  return axios.get('/api/hotel', {
    params
  }).then((response) => {
    return Promise.resolve(response)
  })
}
export const getDiscount = function (ci) {
  const params = {
    theme: 'cheap',
    tab: 'all',
    ci,
    limit: 20
  }
  return axios.get('/api/discount', {
    params
  }).then(response => {
    return Promise.resolve(response)
  })
}
export const getFilms = function (ci) {
  const url = '/film/ptapi/getHotFilms'
  const params = {
    ci,
    limit: 10
  }
  return axios.get(url, {
    params
  }).then(response => {
    return Promise.resolve(response)
  })
}



