<template>
  <el-row class="footer">
    <el-col :span="24">
      <footnavs></footnavs>
    </el-col>
    <el-col :span="24">
      <comfooter></comfooter>
    </el-col>
  </el-row>
</template>
<script>
import footnavs from './footnavs'
import comfooter from './comfooter'
export default {
  components: {
    footnavs,
    comfooter
  }
}
</script>
<style scoped>
  .footer {
    background: #fff;
  }
</style>



