<template>
  <el-row class="register_container">
    <el-col :span="24" class="topbar">
      <div class="left_topbar">
        <img class="avatar" :src="meituan" alt="">
        <span class="avatar_text">美团</span>
      </div>
      <div class="right_topbar">
        <span class="exist_account">已有美团帐号?</span><router-link to="/login" class="login_up">登录</router-link>
      </div>
    </el-col>
    <el-col :span="12">
      <div class="register_form_container">
        <el-form
        ref="ruleForm"
        :model="ruleForm"
        :rules="rules"
        label-width="100px">
          <el-form-item
          label="手机号"
          prop="mobile">
            <el-input
            data-isClear="true"
            type="text"
            v-model="ruleForm.mobile"
            @focus="mobileFocus"
            @blur="mobileBlur($event, 'mobile')"
            autocomplete="new-password">
            </el-input>
            <div class="remain_text">
              <span>
                {{remainText}}
              </span>
            </div>
          </el-form-item>
          <el-form-item
          label="短信动态码"
          prop="verCode">
            <el-input
            autocomplete="new-password"
            type="text"
            v-model="ruleForm.verCode"
            @focus="mobileFocus"
            @blur="mobileBlur($event, 'verCode')">
            </el-input>
            <div class="remain_text">
              <span>
              </span>
              </div>
          </el-form-item>
          <el-button class="verCode_btn" @click="getVerCode">
            免费获取短信动态码
            <span class="code_interval" v-show="codeInterval">({{codeInterval}})</span>
          </el-button>
          <span v-show="verCodeMsg">{{verCodeMsg}}</span>
          <el-form-item
          label="创建密码"
          prop="pasword">
            <el-input
            type="password"
            v-model="ruleForm.pasword"
            :required="true"
            @focus="mobileFocus"
            @blur="mobileBlur($event, 'pasword')"
            autocomplete="off">
            </el-input>
            <div class="remain_text">
              <span>
              </span>
              </div>
          </el-form-item>
          <el-form-item
          label="确认密码"
          prop="confirmPasword">
            <el-input
            type="password"
            :required="true"
            v-model="ruleForm.confirmPasword"
            @focus="mobileFocus"
            @blur="mobileBlur($event, 'confirmPasword')"
            autocomplete="new-password">
            </el-input>
            <div class="remain_text">
              <span>
              </span>
              </div>
          </el-form-item>
          <el-form-item>
            <el-button   class="register_btn" @click="onSubmit">同意以下协议并注册</el-button>
          </el-form-item>
          </el-form>
          <a class="about" href="http://www.meituan.com/about/terms" target="_blank">
            《美团网用户协议》
          </a>
        </div>
    </el-col>
    <el-col :span="24">
      <div class="footer_text">
        ©meituan.com  京ICP证070791号  京公网安备11010502025545号
      </div>
    </el-col>
  </el-row>
</template>
<script>
const VER_CODE_LEN = 6
const VER_CODE_MAX_NUM = 60
export default {
  data () {
    const checkPhone = (rule, val, callback) => {
      const mobileReg = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/
      !mobileReg.test(val) ? callback(new Error('请输入正确的邮箱')) : callback()
    }
    const checkVerCode = (rule, val, cb) => {
      val.toString().length === VER_CODE_LEN ? cb() : cb(new Error('请输入正确的验证码'))
    }
    return {
      meituan: require('./meituan.jpg'),
      remainText: '注册成功后，全美团通用',
      codeInterval: 0,
      verCodeMsg: '',
      lock: true,
      ruleForm: {
        mobile: '',
        verCode: '',
        pasword: '',
        confirmPasword: ''
      },
      rules: {
        mobile: [
          {required: true, message: '请输入邮箱', trigger: 'blur'},
          {validator: checkPhone, type: 'number', trigger: 'blur'}
        ],
        verCode: [
          {validator: checkVerCode, autocomplete: 'off', type: 'number', required: true, message: '填写正确的验证码', trigger: 'blur'}
        ],
        pasword: [
          {required: true, message: '请填写密码！'}
        ],
        confirmPasword: [
          {required: true, message: '请填写确认密码！'}
        ]
      }
    }
  },
  methods: {
    mobileFocus (e) {
      if (e.target.getAttribute('data-isClear') && this.remainText) {
        this.remainText = ''
      }
      const remailBtn = event.target.parentElement.nextElementSibling.firstChild
      remailBtn.className = ''
    },
    mobileBlur (event, prop) {
      const remailBtn = event.target.parentElement.nextElementSibling.firstChild
      this.isShow = true
      this.$refs['ruleForm'].validateField(prop, (isShow) => {
        console.log(isShow)
        remailBtn.className = ''
        !isShow && isShow !== undefined ? remailBtn.classList.add('success') : remailBtn.classList.add('fail')
      })
    },
    async getVerCode () {
      if (this.lock === false) return
      this.lock = false
      setTimeout(() => {
        this.lock = true
      }, 500)
      if (this.codeInterval !== 0) return alert('获取验证码太频繁')
      this.codeInterval = VER_CODE_MAX_NUM
      const timer = setInterval(() => {
        this.codeInterval = Math.max(--this.codeInterval, 0)
        !this.codeInterval && clearInterval(timer)
        if (!this.codeInterval) this.verCodeMsg = ''
      }, 1000)
      const res = await this._getVcodeInfo()
      if (!res.code) this.verCodeMsg = res.data.msg
    },
    async _getVcodeInfo () {
      const verCodeInfos = await this.$http.post('api/users/verCode', {
        mobile: this.ruleForm.mobile
      })
      return verCodeInfos
    },
    onSubmit () {
      this.$refs['ruleForm'].validate((bool, obj) => {
        const {mobile, verCode, pasword, confirmPasword} = this.ruleForm
        if (!bool || !(mobile && verCode && pasword && confirmPasword)) return
        if (pasword !== confirmPasword) return alert('两次密码不一致!')
        const res = this._registerUser(this.ruleForm)
        res.then(res => {
          !res.data.code ? this.$router.push('/login') : alert(res.data.msg)
        })
      })
    },
    async _registerUser (infos) {
      const {mobile, verCode, pasword, confirmPasword} = infos
      const result = await this.$http.post('api/users/register', {
        mobile,
        verCode,
        pasword,
        confirmPasword
      })
      return result
    }
  },
  mounted () {
  //   const inputArr = this.$refs.ruleForm.$el.querySelectorAll('input')
  //   const arr = [...inputArr]
  //  window.onload = () => {
  //    console.log(111111)
  //   arr.map(item => {
  //       item.value = ''
  //     })
  //  }
  }
}
</script>
<style scoped>
/* .register_container {
  padding: 0px 100px;
} */
.topbar {
 height: 60px;
 line-height: 60px;
 padding: 0px 100px;
 border-bottom: 1px solid rgba(153, 153, 153, 0.3);
}
.left_topbar {
  float: left;
}
.avatar {
  vertical-align: middle;
}
.avatar_text {
  font-size: 25px;
  font-weight: 600;
  vertical-align: middle;
}
.right_topbar {
  float: right;
}
.exist_account {
  margin-right: 15px;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
}
.login_up {
  display: inline-block;
  padding: 0 10px;
  background-color: yellow;
  line-height: 30px;
  font-size: 16px;
  font-weight: 400;
}
.register_form_container {
  padding: 60px 100px 0px 100px;
}
.remain_text {
  position: absolute;
  left: 100%;
  bottom: 0px;
  margin-left: 15px;
  white-space: nowrap;
  color: #999;
}
.success, .fail {
  display: inline-block;
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
}
.success {
  background: greenyellow;
}
.success::before, .fail::before {
  content: '';
  display: inline-block;
  box-sizing: border-box;
  height:3px;
  width: 60%;
  background: #fff;
  margin-bottom: 13px;
}
.success::before {
  content: '';
  display: inline-block;
  box-sizing: border-box;
  height: 3px;
  /* width: 60%; */
  padding-left: 15px;
  background: #fff;
  margin-bottom: 13px;
  margin-left: 3px;
}
.success::after {
  content: '';
    display: inline-block;
    position: absolute;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-90deg);
    height: 3px;
    width: 60%;
    background: #fff;
    top: 9px;
    left: 5px;
}
.fail {
  background: orange;
}
.verCode_btn {
  margin: 0px 0px 20px 100px;
  padding: 8px 12px;
}
.code_interval {
  padding-left: 10px;
  color: orange;
}
.register_btn {
  display: block;
  padding: 8px;
  width: 70%;
  background: linear-gradient(135deg, #FFD000 0,#FFBD00  100%);
}
.about {
  display: inline-block;
  margin-left: 100px;
  color: #FE8C00;
}
.footer_text {
  margin-top: 30px;
  padding-top: 30px;
  border-top: 1px solid rgba(153, 153, 153, 0.3);
  text-align: center;
}
</style>



