<template>
  <div class="city">
    <i><img src="./city.png" alt="图标" height="12px"></i>
    <router-link to="/" class="city_item">{{$store.state.geo.currentGeo.name}}</router-link>
    <router-link to="/changeCity">切换城市</router-link>
    <div class="detail_adr">
      <span>[</span>
      <span class="city_item" @click="toggleCity(item)" :key="indx"
        v-for="(item, indx) in $store.state.geo.nearCitys.slice(0, 3)">
        {{item.name}}
      </span>
      <span>]</span>
    </div>
  </div>
</template>
<script>
export default {
  created () {
    this.$store.dispatch('geo/setGeo')
  },
  methods: {
    toggleCity (city) {
      this.$store.commit({
        type: 'geo/toggleGeo',
        city
      })
    }
  }
}
</script>
<style scoped>
  .city {
    text-align: left;
    font-size: 12px;
  }

  .detail_adr {
    display: inline-block;
  }

  .city_item {
    cursor: pointer;
  }
</style>



