<template>
  <el-row class="container" ref="container">
    <el-col :span="19">
      <crumb :city="$store.state.geo.currentGeo.name" :product="$route.params.search"></crumb>
      <catelist @mouseenter.native="showListBox($event)" @mouseleave.native="hideListBox" ref="list" :list="list">
      </catelist>
      <div class="cate_wrapper" ref="cate_wrapper">
        <dl class="cate_list">
          <dt class="title">分类</dt>
          <dt class="title_btn">全部</dt>
          <div class="content_wrapper" v-show="areas.length">
            <dd class="cate_item" @mouseenter="showListBox($event, indx, 'types')" @mouseleave="hideListBox" :key="indx"
              v-for="(cate_list, indx) in types">
              <a href="javascript:;"><span class="cate_text">{{cate_list}}<i class="icon_down"></i></span></a>
            </dd>
          </div>
        </dl>
        <dl class="cate_list">
          <dt class="title">区域</dt>
          <dt class="title_btn">全部</dt>
          <div class="content_wrapper" v-show="types.length">
            <dd class="cate_item" @mouseenter="showListBox($event, indx, 'areas')" @mouseleave="hideListBox" :key="indx"
              v-for="(cate_list, indx) in areas">
              <a href="javascript:;"><span class="cate_text">{{cate_list}}<i class="icon_down"></i></span></a>
            </dd>
          </div>
        </dl>
      </div>
      <el-col :span="24">
        <div class="store_wrapper">
          <dl class="rank">
            <dd class="rank_item" :key="indx" v-for="(rank_item, indx) in rank">
              <span class="rank_item_text" :class="{active: indx === 1, upPrice: upPrice, downPrice: downPrice}"
                @click="toggleStatus(indx, rank_item)">{{rank_item.text}}</span>
            </dd>
          </dl>
          <dl>
            <dd ref="stroe_item_wrapper" class="stroe_item_wrapper" :key="indx" v-for="(item, indx) in store"
              v-if="store.length > 0">
              <store @ready="ready" @imgload="imgload" @show-map="showMap(indx)" :order="Number(indx+1)" :store="item">
              </store>
            </dd>
            <dd class="noStore" v-if="store.length === 0">
              暂无内容
            </dd>
          </dl>
        </div>
      </el-col>
    </el-col>
    <el-col :span="5">
      <div class="map_wrapper" :class="{fixed: isFixed, absolute: isAbsolute}" ref="map_wrapper">
        <div id="smallMap" class="smallMap"></div>
        <div class="pin_zoom" @click="showMap(curIndx)">
          <img class="pin_zoom_icon" :src="pin_zoom" alt="">
        </div>
      </div>
    </el-col>
    <el-col :span="24">
      <div class="map_dialong" v-show="showDialog">
        <div class="big_map_wrapper">
          <div class="del_btn" @click="toggleDialong">X</div>
          <div id="bigMap"></div>
        </div>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import crumb from '@/base/crumb/crumb'
import store from '@/components/product/store'
import catelist from '@/components/product/cate_list'
import { throtle } from '@/public/js/throtle'
export default {
  components: {
    crumb,
    store,
    catelist
  },
  data () {
    return {
      areas: [],
      types: [],
      cateInfos: {},
      rank: [{ kind: 'smartRank', text: '智能排序' }, { kind: 'priceRank', text: '价格排序' }, { kind: 'popularity', text: '人气最高' }, { kind: 'ratingRank', text: '评价最高' }],
      list: {},
      upPrice: false,
      downPrice: false,
      defaultUrl: require('./1.jpg'),
      pin_zoom: require('./pin_zoom.png'),
      timer: null,
      store: [],
      location: [],
      mapText: [],
      isFixed: false,
      isAbsolute: false,
      curIndx: -1,
      bigCurIndx: -1,
      showDialog: false,
      bigMapCurIndx: -1,
      markerList: [],
      markerBigList: [],
      originRank: []
    }
  },
  watch: {
    '$store.state.geo.currentGeo': {
      async handler (val) {
        this._getCateAboutQuery(val)
      },
      deep: true
    },
    '$route.params.search': {
      async handler (keyword) {
        if (keyword === undefined) return
        this.$store.commit({
          type: 'geo/setSearch',
          search: this.$route.params.search
        })
        await this._getResultByKeyword(keyword)
      }
    },
    bigCurIndx: {
      handler (val) {
        const a = this.location.map((item, indx) => {
          if (item.length > 0) {
            return indx
          }
        }).filter(item => item !== undefined)
        const nodeList = document.querySelector('#bigMap').querySelectorAll('.markerText')
        a.map((item, indx) => {
          if (item === val) {
            if (typeof nodeList[indx] !== 'undefined') nodeList[indx].style.display = 'block'
            this.bigMap.setCenter(this.location[item])
          } else {
            if (typeof nodeList[indx] !== 'undefined') nodeList[indx].style.display = 'none'
          }
        })
      }
    },
    curIndx: {
      handler (val) {
        const a = this.location.map((item, indx) => {
          if (item.length > 0) {
            return indx
          }
        }).filter(item => item !== undefined)
        const smallNodeList = document.querySelector('#smallMap').querySelectorAll('.markerText')
        a.map((item, indx) => {
          if (item === val) {
            if (smallNodeList[indx] !== 'undefined') smallNodeList[indx].style.display = 'block'
            this.smallMap.setCenter(this.location[item])
          } else {
            if (typeof smallNodeList[indx] !== 'undefined') smallNodeList[indx].style.display = 'none'
          }
        })
      },
      deep: true
      // immediate: true
    }
  },
  methods: {
    toggleStatus (indx, rank) {
      this.chooseRank(rank)
    },
    chooseRank (rank) {
      switch (rank.kind) {
        case 'smartRank':
          this.store = []
          this.originRank.map(item => {
            this.store.push(item)
          })
          break
        case 'priceRank':
          if (this.upPrice === true) {
            this.upPrice = false
            this.downPrice = true
          } else {
            this.upPrice = true
            this.downPrice = false
          }
          this.store.sort((a, b) => {
            const aCost = isNaN(Number(a.cost)) ? 0 : a.cost
            const bCost = isNaN(Number(b.cost)) ? 0 : b.cost
            const type = this.upPrice === true ? bCost - aCost : aCost - bCost
            return type
          })
          break
        case 'ratingRank':
          this.store.sort((a, b) => {
            const aRating = isNaN(Number(a.rating)) ? 0 : a.rating
            const bRating = isNaN(Number(b.rating)) ? 0 : b.rating
            return bRating - aRating
          })
          break
        case 'popularity':
          this.store.sort((a, b) => {
            const aCommentCount = isNaN(Number(a.commentCount)) ? 0 : a.commentCount
            const bCommentCount = isNaN(Number(b.commentCount)) ? 0 : b.commentCount
            return bCommentCount - aCommentCount
          })
          break
      }
      this.location = []
      this.mapText = []
      if (rank.kind !== 'priceRank') {
        this.upPrice = false
        this.downPrice = false
      }
      this.store.map(item => {
        this.location.push(item.location)
        this.mapText.push(item.title)
      })
    },
    showListBox (e, indx, type) {
      if (type === 'areas') {
        this.list = {
          title: this.areas[indx],
          content: this.cateInfos.areas.filter(item => item.type === this.areas[indx])[0].module.slice(0, 20)
        }
      } else if (type === 'types') {
        this.list = {
          title: this.types[indx],
          content: this.cateInfos.types.filter(item => item.type === this.types[indx])[0].module.filter(item => item !== '').slice(0, 20)
        }
      }
      if (e.target === this.$refs.list.$el) {
        return clearTimeout(this.timer)
      }
      setTimeout(() => {
        this.$refs.list.$el.style.display = 'block'
        const { left, top } = this._getPos(e.target)
        const height = e.target.offsetHeight - document.documentElement.scrollTop
        this.$refs.list.$el.style.left = left + 'px'
        this.$refs.list.$el.style.top = top + height + 'px'
        window.addEventListener('scroll', () => {
          const height = e.target.offsetHeight - document.documentElement.scrollTop
          this.$refs.list.$el.style.left = left + 'px'
          this.$refs.list.$el.style.top = top + height + 'px'
        })
      }, 150)
    },
    _getPos (domEl) {
      if (!domEl) return
      if (domEl == null) return
      let left = 0
      let top = 0
      while (domEl !== document.body) {
        left += domEl.offsetLeft
        top += domEl.offsetTop
        domEl = domEl.offsetParent
      }
      return {
        left,
        top
      }
    },
    hideListBox (e) {
      this.timer = setTimeout(() => {
        this.$refs.list.$el.style.display = 'none'
      }, 150)
    },
    async _getCateAboutQuery () {
      const name = this.$store.state.geo.currentGeo.name
      const city = name && name.replace(/市$/, '')
      if (city === undefined) return
      const url = 'http://cp-tools.cn/categroy/crumbs'
      const res = await this.$http.get(url, {
        params: {
          city
        }
      })
      const { data: { areas, types } } = res
      this.cateInfos = res.data
      this.areas = areas.map(item => item.type)
      this.types = types.map(item => item.type)
    },
    async _getResultByKeyword (keyword) {
      const name = this.$store.state.geo.currentGeo.name
      let city = name && name.replace(/市$/, '')
      this._getCateAboutQuery()
      const { data: { pois } } = await this.$http.get('http://cp-tools.cn/search/resultsByKeywords', {
        params: {
          keyword,
          city
        }
      })
      this.location = []
      this.mapText = []
      this.store = pois.filter(item => item.photos.length > 0).map(item => {
        const { name, biz_ext: { rating, cost }, address, entr_location: entrLocation, photos, type } = item
        const location = entrLocation.length === 0 ? [] : entrLocation.split(',')
        this.location.push(location)
        this.mapText.push(name)
        return {
          title: name,
          rating: Number(rating),
          address,
          cost: Number(cost) !== 0 ? cost : '暂无价格',
          type: type.split(';')[0],
          location,
          url: photos[0].url,
          commentCount: Math.floor(Math.random() * 1000)
        }
      })
      this.originRank = this.store
    },
    toggleMapClass () {
      const container = this.$refs.container.$el
      const top = 290
      const height = container.offsetHeight
      window.addEventListener('scroll', (e) => {
        const scrollTp = document.documentElement.scrollTop
        const dirTop = scrollTp - top
        if (dirTop >= 0 && dirTop <= (height - top - 40)) {
          this.isFixed = true
          this.isAbsolute = false
        } else if (dirTop > (height - top - 40)) {
          this.isFixed = false
          this.isAbsolute = true
        } else {
          this.isFixed = false
          this.isAbsolute = false
        }
      })
    },
    ready () {
      throtle(() => {
        this.toggleMapText()
      })
    },
    imgload () {
      this.toggleMapClass()
    },
    toggleMapText () {
      const stroeItemWrapper = this.$refs.stroe_item_wrapper
      const pos = []
      stroeItemWrapper.map(item => {
        const { top } = this._getPos(item)
        pos.push(top)
      })
      this.toggleCurinx = () => {
        const top = document.documentElement.scrollTop
        this.curIndx = this.nowPosIndx(pos, top)
      }
      window.addEventListener('scroll', this.toggleCurinx)
    },
    nowPosIndx (nodes, top) {
      return nodes.filter(item => item - 300 < top).length - 1
    },
    showMap (indx) {
      this.toggleDialong(indx)
    },
    toggleDialong (indx) {
      this.bigCurIndx = indx
      this.showDialog = !this.showDialog
    },
    _setBigMarker (location, map) {
      location.map((item, indx) => {
        if (item.length === 0) return
        const content = `<div style="
    position: relative;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    background: orange;
    border-radius: 50%;
    color: #fff;
    font-weight: 400;">
      ${indx + 1}
      <span style="
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 90%;
      margin-top: -1px;
      border: 10px solid transparent;
      border-top-color: orange;
      ">
      </span>
    </div>`
        /* eslint-disable no-undef */
        const marker = new AMap.Marker({
          content,
          position: item
        })
        const content1 = `<div class="markerText" style="
    display: none;
    position: relative;
    text-align: center;
    z-index: 100;
    width: 200px;
    line-height: 60px;
    height: 60px;
    background: orange;
    border-radius: 8px;
    font-size: 25px;
    color: #fff;">
      <span style="
      content: '';
      position: absolute;
      width: 25px;
      height: 25px;
      background: orange;
      left: 50%;
      bottom: -5px;
      transform: translateX(-50%) rotate(45deg);">
      </span>
      <span
        style="
          position: absolute;
          left: 0px;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          ">
          ${this.mapText[indx]}
      </span>
    </div>`
        const marker1 = new AMap.Marker({
          content: content1,
          position: item,
          zIndex: 100,
          offset: new AMap.Pixel(-94, -110)
        })
        this.markerBigList.push(marker, marker1)
      })
      map.add(this.markerBigList)
    },
    _setMarker (location, map) {
      location.map((item, indx) => {
        if (item.length === 0) return
        const content = `<div style="
    position: relative;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    background: orange;
    border-radius: 50%;
    color: #fff;
    font-weight: 400;">
      ${indx + 1}
      <span style="
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 90%;
      margin-top: -1px;
      border: 10px solid transparent;
      border-top-color: orange;
      ">
      </span>
    </div>`
        const marker = new AMap.Marker({
          content,
          position: item
        })
        const content1 = `<div class="markerText" style="
    display: none;
    position: relative;
    text-align: center;
    z-index: 100;
    width: 100px;
    line-height: 30px;
    height: 30px;
    background: orange;
    border-radius: 4px;
    color: #fff;">
      <span style="
      content: '';
      position: absolute;
      width: 25px;
      height: 25px;
      background: orange;
      left: 50%;
      bottom: -5px;
      transform: translateX(-50%) rotate(45deg);">
      </span>
      <span
        style="
          position: absolute;
          left: 0px;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          ">
          ${this.mapText[indx]}
      </span>
    </div>`
        const marker1 = new AMap.Marker({
          content: content1,
          position: item,
          zIndex: 100,
          offset: new AMap.Pixel(-45, -70)
        })
        this.markerList.push(marker, marker1)
      })
      map.add(this.markerList)
    },
    _setCenter (location, map) {
      map.setCenter(location[0])
    }
  },
  async created () {
    const keyword = this.$route.params.search
    await this._getResultByKeyword(keyword)
    this.$store.commit({
      type: 'geo/setSearch',
      search: this.$route.params.search
    })
  },
  deactivated () {
    window.removeEventListener('scroll', this.toggleCurinx)
  },
  mounted () {
    window.onLoad = () => {
      // location = location.length > 0 ? location[0] : ['118.15267', '24.532938']
      this.bigMap = new AMap.Map('bigMap', {
        zoom: 11
      })

      this.smallMap = new AMap.Map('smallMap', {
        zoom: 11
      })

      const initLocation = this.location.filter(item => item.length > 0)
      initLocation.length && this._setCenter(initLocation, this.bigMap)
      initLocation.length && this._setBigMarker(this.location, this.bigMap)
      initLocation.length && this._setCenter(initLocation, this.smallMap)
      initLocation.length && this._setMarker(this.location, this.smallMap)
      this.$watch('location', (val) => {
        const location = val.filter(item => item.length > 0)
        if (this.markerList.length > 0) {
          this.bigMap.remove(this.markerList)
          this.smallMap.remove(this.markerList)
          this.markerList = []
        }
        location.length && this._setCenter(location, this.smallMap)
        location.length && this._setMarker(val, this.smallMap)
        location.length && this._setCenter(location, this.bigMap)
        location.length && this._setBigMarker(val, this.bigMap)
      })
      AMap.plugin([
        'AMap.Scale'
      ], () => {
        this.smallMap.addControl(new AMap.Scale())
      })
      AMap.plugin([
        'AMap.Scale',
        'AMap.ToolBar',
        'AMap.MapType'
      ], () => {
        this.bigMap.addControl(new AMap.Scale())
        this.bigMap.addControl(new AMap.ToolBar())
        this.bigMap.addControl(new AMap.MapType())
      })
    }
    const url = 'https://webapi.amap.com/maps?v=1.4.15&key=f0d5313186f411d406a7f7df5da7007c&callback=onLoad'
    const jsapi = document.createElement('script')
    jsapi.charset = 'utf-8'
    jsapi.src = url
    document.head.appendChild(jsapi)
  }
}
</script>
<style scoped>
  .store_wrapper {
    margin-top: 20px;
    padding: 10px;
    border: 1px solid rgba(102, 102, 102, 0.1);
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(102, 102, 102, 0.1);
  }

  .rank {
    padding-bottom: 20px;
    border-bottom: 2px solid rgba(102, 102, 102, 0.1);
  }

  .rank_item {
    position: relative;
    display: inline-block;
    padding-right: 25px;
    height: 25px;
    line-height: 25px;
    cursor: pointer;
  }

  .rank_item_text {
    font-size: 16px;
  }

  .active::before,
  .active::after {
    content: '';
    position: absolute;
    right: 12px;
    border: 4px solid;
    border-color: transparent;
  }

  .active::before {
    top: 15px;
    border-top-color: rgba(102, 102, 102, 0.5);
  }

  .active::after {
    top: 5px;
    border-bottom-color: rgba(102, 102, 102, 0.5);
  }

  .upPrice.active::after {
    top: 5px;
    border-bottom-color: black;
  }

  .downPrice.active::before {
    top: 15px;
    border-top-color: black;
  }

  .container {
    margin: 0px 5% 0px 5%;
    padding-top: 50px;
    position: relative;
  }

  .cate_wrapper {
    margin-top: 25px;
    padding: 15px;
    border: 1px solid rgba(153, 153, 153, 0.1);
    box-shadow: 0px 0px 3px rgba(153, 153, 153, 0.5);
  }

  .cate_list {
    padding-left: 150px;
    min-height: 50px;
    font-size: 0px;
  }

  .content_wrapper {
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(153, 153, 153, 0.5);
  }

  .cate_list:last-child .content_wrapper {
    border-bottom: none;
    padding-bottom: 0px;
  }

  .title,
  .title_btn {
    position: absolute;
    margin-top: 5px;
  }

  .title {
    left: 20px;
    margin-top: 10px;
  }

  .title_btn {
    left: 80px;
    padding: 5px 10px;
    border-radius: 30%;
    background: linear-gradient(to bottom right, #FFD000, #FFBD00);
  }

  .cate_item {
    position: relative;
    display: inline-block;
    /* margin-right: 40px; */
    width: 16%;
    font-size: 12px;
  }

  .cate_item a {
    display: block;
    padding: 10px 0px;
    text-align: left;
  }

  .cate_text {
    position: relative;
    font-size: 14px;
  }

  .icon_down {
    position: absolute;
    top: 7px;
    margin-left: 5px;
    border: 5px solid;
    border-color: transparent;
    border-top-color: rgba(102, 102, 102, 0.3);
  }

  .stroe_item_wrapper {
    border-bottom: 1px solid rgba(102, 102, 102, 0.3);
  }

  .stroe_item_wrapper:last-child {
    border-bottom: none;
  }

  .noStore {
    width: 100%;
    box-sizing: border-box;
    padding: 20px;
    height: 200px;
    font-size: 16px;
    color: #666;
  }

  .map_wrapper {
    position: relative;
    padding-top: 40px;
    margin: 0 auto;
  }

  .map_wrapper.fixed {
    top: -40px;
    /* top: 0px; */
    position: fixed;
  }

  .map_wrapper.absolute {
    position: absolute;
    bottom: 0px;
  }

  .map_dialong {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 100;
  }

  .big_map_wrapper {
    position: absolute;
    left: 50%;
    bottom: 0px;
    transform: translateX(-50%);
    padding: 50px 10px 0px 10px;
    width: 80%;
    height: 80%;
    background-color: rgb(68, 67, 67);
  }

  .del_btn {
    position: absolute;
    top: 0px;
    right: 15px;
    height: 50px;
    line-height: 50px;
    font-size: 36px;
    color: #fff;
    font-weight: 400;
    cursor: pointer;
  }

  .big_map {
    left: 50%;
    bottom: 0px;
    transform: translateX(-50%);
    box-shadow: 0 0 10px black;
  }

  .pin_zoom {
    position: absolute;
    /* right: 10px; */
    margin-left: 200px;
    bottom: 10px;
    cursor: pointer;
  }

  .pin_zoom_icon {
    display: inline-block;
    width: 40px;
    /* margin-top: -5px; */
  }

  #bigMap {
    width: 100%;
    height: 100%;
    box-shadow: 0 0 10px #666;
  }

  #smallMap {
    margin-left: 15px;
    width: 250px;
    height: 250px;
    box-shadow: 0 0 10px #666;
  }
</style>



