<template>
  <el-row>
    <el-col :span="24">
      <titlebar @toggleInfo="_toggleInfo" :list="titlebarInfo.infoTitle" :listTitle="titlebarInfo.listTitle"
        :bgColor="titlebarInfo.bgColor" :color="titlebarInfo.color"></titlebar>
    </el-col>
    <el-col :span="8" :key="indx" v-for="(item,indx) in listData">
      <my-img :imgUrl="item.imgUrl">
        <div class="info">
          <h5 class="title">
            {{item.title}}
          </h5>
          <div class="subTitle">
            {{item.subTitle}}
          </div>
          <span class="price_wrpper">
            <span class="current_price">{{item.currentPrice}}</span>
          </span>
          <span class="bottomInfo">{{item.bottomInfo}}</span>
        </div>
      </my-img>
    </el-col>
    <el-col :span="24">
      <titlebar @toggleInfo="_toggleDiscountInfo" :listTitle="discountTitleBar.listTitle"
        :bgColor="discountTitleBar.bgColor" :color="discountTitleBar.color" :list="discountTitleBar.infoTitle">
      </titlebar>
    </el-col>
    <el-col :span="24">
      <div class="discount_wrapper" :key="indx" v-for="(item,indx) in discountData">
        <my-img :imgUrl="item.imgUrl" imgHeight="120px">
          <div class="info">
            <h5 class="title">
              {{item.title}}
            </h5>
            <div class="subTitle">
              {{item.subTitle}}
            </div>
            <span class="price_wrpper">
              <span class="current_price">{{item.currentPrice}}</span>
            </span>
            <span class="bottomInfo">{{item.bottomInfo}}</span>
          </div>
        </my-img>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import titlebar from './titlebar'
import myImg from './img'
export default {
  components: {
    titlebar,
    myImg
  },
  data () {
    return {
      titlebarInfo: {
        listTitle: '有格调',
        color: '#fff',
        bgColor: '#bea474',
        infoTitle: [{ text: '全部', kind: 'all' }, { text: '约会聚餐', kind: '美食' }, { text: '丽人spa', kind: '丽人' }, { text: '电影', kind: 'movie' }, { text: '品质出游', kind: '旅游' }]
      },
      discountTitleBar: {
        listTitle: '狠优惠',
        color: '#fff',
        bgColor: '#fc4843',
        infoTitle: [{ text: '全部', kind: 'all' }, { text: '美食', kind: '吃饭' }, { text: '休闲', kind: '休闲' }]
      },
      listData: [],
      discountData: []
    }
  },
  watch: {
    '$store.state.geo.currentGeo.name': {
      async handler (city) {
        const keyword = this.discountTitleBar.infoTitle[0].kind
        const discountKeyword = this.titlebarInfo.infoTitle[0].kind
        this.listData = await this._getInfos(city, keyword)
        this.discountData = await this._getInfos(city, discountKeyword, 10)
      },
      deep: true
    }
  },
  methods: {
    async _toggleInfo (indx) {
      const city = this.$store.state.geo.currentGeo.name
      const keyword = this.titlebarInfo.infoTitle[indx].kind
      this.listData = await this._getInfos(city, keyword)
    },
    async _toggleDiscountInfo (indx) {
      const city = this.$store.state.geo.currentGeo.name
      const keyword = this.discountTitleBar.infoTitle[indx].kind
      this.discountData = await this._getInfos(city, keyword, 10)
    },
    async _getInfos (city, keyword, min = 6) {
      let listData = []
      city = city.replace(/市$|省$/, '')
      const res = await this.$http.get('http://cp-tools.cn/search/resultsByKeywords', {
        params: {
          keyword,
          city
        }
      })
      const { pois } = res.data
      const a = pois.filter(item => item.photos.length > 0)
      listData = a.slice(0, Math.min(min, a.length)).map(item => {
        const { name, type, address, adname, photos: [{ url }] } = item
        return {
          title: name,
          subTitle: type,
          currentPrice: address,
          bottomInfo: adname,
          imgUrl: url
        }
      })
      return listData
    }
  }
}
</script>
<style scoped>
  .info {
    padding-left: 15px;
  }

  .title,
  .subTitle {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .title {
    padding: 15px 0px;
    font-size: 16px;
    text-align: left;
  }

  .subTitle {
    padding: 10px 0px;
    color: #999;
  }

  .price_wrpper {
    padding-bottom: 15px;
    display: inline-block;
    color: burlywood;
    overflow: hidden;
    width: 80%;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .current_price {
    font-size: 14px;
  }

  .old_price {
    color: #999;
    text-decoration: line-through;
  }

  .bottomInfo {
    color: #999;
    position: absolute;
    right: 10px;
    bottom: 10px;
  }

  .discount_wrapper {
    display: inline-block;
    width: 20%;
  }
</style>



