<template>
  <el-row class="search_container">
    <el-col :span="6">
      <el-row>
        <el-col :span="14">
          <div class="avatar_wrapper">
            <img class="logo" src="./logo.png" alt="logo" height="46px">
          </div>
        </el-col>
        <el-col :span="10">
          <div class="cetegorires_wrapper">
            <div class="cetegorires_btn" @mouseenter="toggleIconClass(false)" @mouseleave="toggleIconClass(true)">
              <span class="cat_text">
                <span>全部分类</span>
                <i class="iconfont" :class="{'iconicon-arrow_up':!iconClaStatus, 'iconicon-arrow_down': iconClaStatus}">
                </i>
              </span>
              <ul class="cetegorires_list">
                <li class="cetegorires_item" :key="indx" v-for="(cetegorires_item, indx) in  cetegorires">
                  <router-link class="link" to="/">{{cetegorires_item.name}}</router-link>
                </li>
              </ul>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-col>
    <el-col :span="12">
      <div class="search_warpper">
        <el-input class="search_box" v-model="queryStr" @blur="toggleStatus(false)" @input="getSuggest" @focus="toggleStatus(true)"
          placeholder="请输入内容">
        </el-input>
        <el-button type="primary" @click="search('')" class="search_btn" icon="el-icon-search"></el-button>
        <div class="recommend">
          <span class="recommend_item" :key="indx" @click="search(recommend_item.name)"
            v-for='(recommend_item, indx) in recommendData'>
            {{recommend_item.name}}
          </span>
        </div>
        <div class="recommend_list" :class="{showList:claStatus}" v-show="!queryStr">
          <template v-if="searchHis.length">
            <h4 class="recom_title">
              最近浏览
            </h4>
            <div class="recommend">
              <span class="recommend_item" :key="recommend_item" @click="search(recommend_item)"
                v-for='recommend_item in searchHis'>
                {{recommend_item}}
              </span>
            </div>
          </template>
          <h4 class="recom_title">
            热门搜索
          </h4>
          <div class="recommend">
            <span class="recommend_item" :key="indx" @click="search(recommend_item.name)"
              v-for='(recommend_item, indx) in recommendData'>
              {{recommend_item.name}}
            </span>
          </div>
        </div>
        <ul class="hot_search" v-show="queryStr&&claStatus">
          <li class="hot_search_item" :key="hot_search_item + Math.random()" @click="search(hot_search_item.name)" v-for='hot_search_item in hotSeach'>
            <router-link to="/" class="link">{{hot_search_item.name}}</router-link>
          </li>
        </ul>
      </div>
      <ul class="cates_navs" v-show="/^\/$/.test($route.fullPath)">
        <li class="cates_navs_item"><router-link class="link" to="/">美团外卖</router-link></li>
        <li class="cates_navs_item"><router-link class="link" to="/">猫眼电影</router-link></li>
        <li class="cates_navs_item"><router-link class="link" to="/">美团酒店</router-link></li>
        <li class="cates_navs_item"><router-link class="link" to="/">民宿／公寓</router-link></li>
        <li class="cates_navs_item"><router-link class="link" to="/">商家入驻</router-link></li>
        <li class="cates_navs_item"><router-link class="link" to="/">美团公益</router-link></li>
      </ul>
    </el-col>

    <el-col :span="4">
      <div class="grid-content bg-purple"></div>
    </el-col>
  </el-row>
</template>
<script>
import '@/public/fonts/icon.css'
import {throtle} from '@/public/js/throtle'
export default {
  data () {
    return {
      queryStr: '',
      searchHis: [],
      iconClaStatus: true,
      recommendData: [],
      hotSeach: [],
      claStatus: false,
      cetegorires: []
    }
  },
  watch: {
    '$store.state.geo.currentGeo' () {
      try {
        this.getRecommend()
      } catch (err) {
        console.log(err)
      }
    }
  },
  created () {
    this.$http.get('http://cp-tools.cn/geo/menu').then((response) => {
      const {menu} = response.data
      this.cetegorires = menu
    })
  },
  methods: {
    async toggleStatus (statu) {
      if (statu === false) {
        setTimeout(() => {
          this.claStatus = statu
        }, 150)
      } else {
        this.claStatus = statu
      }
      await this.getSuggest()
    },
    async search (val = '') {
      const queryStr = val !== '' ? val : this.queryStr
      if (queryStr === '') return
      const indx = this.searchHis.findIndex(item => item === queryStr.toLowerCase())
      if (indx !== -1) {
        this.searchHis.splice(indx, 1)
      }
      this.searchHis.unshift(queryStr.toLowerCase())
      console.log(this.searchHis)
      this.queryStr = queryStr
      this.$router.push('/product/' + this.queryStr)
    },
    toggleIconClass (status) {
      this.iconClaStatus = status
    },
    async getSuggest () {
      throtle(async () => {
        if (this.queryStr === '') return
        const input = this.queryStr
        const city = this.$store.state.geo.currentGeo.name.replace(/市$|省$/, '')
        const res = await this.$http.get('http://cp-tools.cn/search/top', {
          params: {
            input,
            city
          }
        })
        const {code, top} = res.data
        if (code === 0) {
          this.hotSeach = top.slice(0, 10) || []
        }
      })
    },
    async getRecommend () {
      const curCity = this.$store.state.geo.currentGeo.name.replace(/市$|省$/, '')
      const res = await this.$http.get('http://cp-tools.cn/search/hotPlace', {
        params: {
          city: curCity
        }
      })
      const {code, result} = res.data
      if (code === 0) {
        this.recommendData = result
      }
    }
  }
}
</script>
<style scoped>
  .search_container {
    padding: 50px 5% 20px 5%;
    position: relative;
    box-shadow: 0px 0px 5px #555;
  }

  .search_warpper {
    position: relative;
    margin: 0 50px;
  }

  .search_box {
    border: 1px solid #13D1BE;
    box-shadow: 0px 0px 0px;
    border-radius: 4px;
    width: 100%;
  }

  .search_box:hover {
    border: 1px solid #13D1BE;
  }

  .el-input__inner:focus {
    border: 1px solid transparent;
  }

  .search_btn {
    position: absolute;
    top: 1px;
    right: -1px;
    width: 15%;
    border-top-left-radius: 0px;
    border-color: #13D1BE;
    background-color: #13D1BE;
  }

  .search_btn:hover,
  .search_btn:focus,
  .search_btn:active {
    background-color: #13D1BE;
    border-color: #13D1BE;
  }

  .recommend {
    margin: 0px 0px 15px 10px;
  }

  .recommend_list {
    display: none;
    position: absolute;
    z-index: 10;
    width: 85%;
    top: 42px;
    padding-top: 20px;
    /* padding: 20px 0px; */
    background-color: #fff;
    box-shadow: 0px 0px 2px rgb(144, 146, 144);
    border-radius: 0px 0px 4px 4px;
  }

  .showList {
    display: block;
  }

  .recom_title {
    margin: 0px;
    margin-left: 10px;
  }
  .recommend {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .recommend_item {
    cursor: pointer;
    display: inline-block;
    padding-top: 15px;
    padding-right: 8px;
    white-space: nowrap;
    padding-left: 0px;
  }

  .recommend_item:hover {
    color: #13D1BE;
  }

  .hot_search {
    position: absolute;
    z-index: 10;
    top: 43px;
    padding-left: 10px;
    width: 83%;
    box-shadow: 0px 0px 4px #555;
    background-color: #fff;
  }

  .link {
    padding: 10px 0px;
  }

  .link:hover {
    color: #13D1BE;
  }

  .cetegorires_wrapper {
    margin-top: 17px;
  }

  .cetegorires_btn {
    position: relative;
    display: inline-block;
    text-align: center;
    border: 1px solid #ccc;
  }

  .cat_text {
    display: inline-block;
    /* padding: 4px 3px; */
    padding: 5px 5px;
  }

  .cetegorires_btn:hover>.cetegorires_list {
    display: block;
  }

  .cetegorires_btn:hover::after {
    content: '';
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 100%;
    height: 2px;
    background-color: #fff;
  }

  .cetegorires_list {
    display: none;
    position: absolute;
    z-index: 10;
    left: 0px;
    background-color: #fff;
    text-align: left;
    box-shadow: 0px 0px 2px #555;
  }

  .cetegorires_list .link {
    padding: 8px 10px;
  }

  .cates_navs {
    margin-left: 50px;
    overflow: hidden;
  }

  .cates_navs_item {
    float: left;
    width: 16%;
  }

  .cates_navs_item .link {
    display: inline-block;
    padding: 5px;
    padding-left: 0px;
    font-size: 16px;
    color: rgb(31, 29, 29);
    font-weight: 600;
  }
  .cates_navs_item .link:hover {
    color: #fbc700;
  }
</style>



