<template>
  <el-table class="order_container" :data="orderList">
    <el-table-column prop="orderNo" label="单号" width="180">
    </el-table-column>
    <el-table-column prop="name" label="商品名">
    </el-table-column>
    <el-table-column prop="price" width="150" label="价格">
    </el-table-column>
    <el-table-column prop="count" label="数量">
      <template slot-scope="scope">
        <el-input-number v-model="scope.row.count" :min="1">
        </el-input-number>
      </template>
    </el-table-column>
    <el-table-column label="总价">
      <template slot-scope="scope">
        <span class="total">{{scope.row.count * scope.row.price}}</span>
      </template>
    </el-table-column>
    <el-table-column width="150" label="操作">
      <el-button type="primary" @click="getOrderList" plain>提交</el-button>
    </el-table-column>
  </el-table>
</template>
<script>
export default {
  data () {
    return {
      orderList: []
    }
  },
  created () {
    this.$http.get('/api/order/list', {
      params: {
        orderNo: this.$route.params.id
      }
    }).then((response) => {
      const { status, data: { code, res } } = response
      if (status !== 200 || code !== 0) return
      console.log(res)
      const { orderNo, name, price, count, user } = res
      this.orderList.push({
        orderNo,
        name,
        price,
        count,
        user
      })
      console.log(this.orderList)
    })
  },
  methods: {
    async getOrderList () {
      const { count, price, name, user, orderNo } = this.orderList[0]
      const total = count * price
      const { status, data: { code, res } } = await this.$http.post('/api/order/orderList', {
        name,
        price,
        count,
        user,
        orderNo,
        total
      })
      if (status === 200 && code === 0) {
        this.$router.push('/orderList/' + res)
      }
    }
  }
}
</script>
<style scoped>
  .order_container {
    margin: 15px auto;
    width: 80%;
  }

  .order_container td {
    text-align: center;
  }

  .total {
    font-size: 500;
    color: orange;
  }
</style>



