<template>
  <div class="menu_wrapper">
    <div class="menu_list">
      <h5 class="title">全部分类</h5>
      <ul class="menu_navs" @mouseenter="show_detail" @mouseover="toggleDetaiInfo" @mouseleave="hide_detail">
        <li class="menu_navs_item" :key="idex" :data-index="idex" v-for="(menu_item, idex) in menusData">
          <i :class="menu_item.type" class="iconfont  icon"></i>
          <span>
            {{menu_item.name}}
          </span>
          <i class="iconfont iconprotect right icon"></i>
        </li>
      </ul>
    </div>
    <div class="detail" v-show="det_bool" data-id="detail" @mouseenter="cancelTimer(timer)" @mouseleave="hide_detail">
      <template v-for="(item_info, idex) in del_info">
        <h5 class="del_title" :key="idex">
          {{item_info.title}}
          <span class="more">更多<i class="iconfont iconprotect"></i></span>
        </h5>

        <div class="del_content"  :key="idex+Math.random()">
          <span class="del_text" :key="idex" v-for="(text, idex) in item_info.child">{{text}}</span>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import '@/public/fonts/icon.css'
export default {
  data () {
    return {
      menusData: [],
      det_bool: false,
      del_info: [],
      timer: ''
    }
  },
  created () {
    this.$http.get('http://cp-tools.cn/geo/menu').then((response) => {
      const {menu} = response.data
      this.menusData = menu
    })
  },
  methods: {
    show_detail (event) {
      this.timer && this.cancelTimer(this.timer)
      this.det_bool = true
    },
    hide_detail (event) {
      this.timer = setTimeout(() => {
        this.det_bool = false
      }, 150)
    },
    cancelTimer (timer) {
      clearTimeout(timer)
    },
    toggleDetaiInfo (event) {
      const mineIndx = event.target.getAttribute('data-index')
      const parentIndx = event.target.parentElement.getAttribute('data-index')
      const indx = mineIndx !== null ? mineIndx : parentIndx
      this.del_info = this.menusData[indx].child
    }
  }
}
</script>
<style scoped>
  .menu_wrapper {
    position: relative;
    /* overflow-x: hidden; */
    width: 100%;
    background: linear-gradient(45deg, rgba(55, 158, 189, 0.8), rgba(39, 192, 171, 0.8));
    color: #fff;
  }

  .title {
    margin-top: -50px;
    height: 70px;
    line-height: 70px;
    font-size: 14px;
    font-weight: 600;
  }

  .icon {
    color: rgb(216, 210, 210)
  }

  .right {
    float: right;
  }

  .menu_navs_item {
    padding: 5px 10px;

  }

  .menu_navs_item:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }

  .menu_navs_item:hover .icon {
    color: #fff;
  }

  .detail {
    position: absolute;
    z-index: 10;
    padding: 0 10px;
    /* display: none; */
    top: 70px;
    left: 100%;
    width: 350px;
    /* height: 86%; */
    height: 417px;
    background-color: #fff;
    color: #999;
  }

  .del_title {
    height: 30px;
    line-height: 30px;
    padding: 15px 0px;
    color: #222;
    border-bottom: 1px solid #e5e5e5;
  }

  .more {
    position: absolute;
    vertical-align: top;
    right: 5px;
    font-size: 12px;
    cursor: pointer;
  }

  .del_content {
    margin-top: 15px;
  }

  .del_text {
    display: inline-block;
    padding-right: 8px;
    padding-top: 8px;
    white-space: nowrap;
    cursor: pointer;
  }
</style>



