<template>
  <div class="user">
    <template v-if="!user">
      <router-link to="/login" class="login">
        立即登录
      </router-link>
      <router-link to="/register" class="register">
        注册
      </router-link>
    </template>
    <template v-else>
      <span class="user_name">
        {{user}}
      </span>
      <span class="login_out" @click="loginOut">
        退出
      </span>
    </template>
  </div>
</template>
<script>
export default {
  data: function () {
    return {
      user: ''
    }
  },
  created () {
    this.$http.get('api/users/info').then(res => {
      if (res.data.result !== null) this.user = res.data.result.mobile
    })
  },
  methods: {
    loginOut () {
      this.$http.get('api/users/exit').then(res => {
        if (!res.data.code) this.user = ''
      })
    }
  }
}
</script>
<style>
  .user {
    font-size: 12px;
  }

  .login,
  .user_name {
    color: #31BBAC;
  }
  .login_out {
    cursor: pointer;
  }
</style>



