<template>
  <el-row class="container">
    <el-col :sapn="24" class="choose_btn_wrapper">
      <span class="title">按省份选择</span>
      <el-select v-model="pvalue" filterable popper-class="province" class="input" @change="getCities(pvalue)"
        placeholder="省份">
        <el-option class="province_item" v-for="item in province" :key="item.id" :label="item.name" :value="item.name">
        </el-option>
      </el-select>
      <el-select v-model="cvalue" filterable class="input" popper-class="chang_city" :disabled="cityIsShow"
        @change="linkToHome($event, cvalue)" placeholder="城市">
        <el-option class="province_item" v-for="item in cities" :key="item.id" :label="item.name" :value="item.name">
        </el-option>
      </el-select>
      <span class="title">
        直接搜索：
      </span>
      <el-select v-model="value" filterable remote reserve-keyword placeholder="请输入关键词" :remote-method="remoteMethod"
        @change="linkToHome($event, value)" :loading="loading">
        <el-option v-for="item in qeuryList" :key="item.id" :label="item.name" :value="item.name">
        </el-option>
      </el-select>
    </el-col>
    <el-col :sapn="24" class="visited_wrapper">
      <div class="recently_title">
        最近访问 :
      </div>
      <dl @click="linkToHome" class="recently_list">
        <dd class="recently_item" v-for="(item, indx) in recentlyVisit" :key="indx" v-show="recentlyVisit.length">
          <router-link to="/">{{item}}</router-link>
        </dd>
      </dl>
    </el-col>
    <el-col :sapn="24">
      <dl class="letter_wrapper">
        <dd class="letter_title">按拼音首字母选择：</dd>
        <dt class="letter_item" v-for="item in letter" :key="item">
          <a :href="'#city_'+item">{{item}}</a>
        </dt>
      </dl>
      <dl class="fomat_city_wrapper" @click="linkToHome" :key="indx" v-for="(item, indx) in formatCitiedAboutLetter">
        <dt class="city_title" :id="'city_'+item[0].title">
          {{item[0].title}}
        </dt>
        <dd class="city_item" :key="indx" v-for="(city, indx) in item">
          <router-link to='/' :data-p="city.province">{{city.name}}</router-link>
        </dd>
      </dl>
    </el-col>
  </el-row>
</template>
<script>
import pinyin4js from 'pinyin4js'
export default {
  data () {
    return {
      province: [],
      cities: [],
      allCities: [],
      value: [],
      qeuryList: [],
      formatCitiedAboutLetter: [],
      recentlyVisit: [],
      letter: 'ABCDEFGHJKLMNPQRSTWXYZ'.split(''),
      loading: false,
      pvalue: '',
      cvalue: ''
    }
  },
  computed: {
    cityIsShow () {
      return this.pvalue === ''
    }
  },
  created () {
    this.$http.get('http://cp-tools.cn/geo/province').then(res => {
      const { province } = res.data
      this.province = province
    })
  },
  mounted () {
    document.querySelector('.province').style.width = '300px'
    document.querySelector('.chang_city').style.width = '600px'
    this.$http.get('http://cp-tools.cn/geo/city').then(res => {
      const { code, city } = res.data
      if (code === 0) {
        this.allCities = city
        this.formatCitiedByLetter()
      }
    })
  },
  methods: {
    linkToHome (e, inputVal) {
      let linkVal
      let p
      if (inputVal === undefined) {
        linkVal = e.target.innerText
        p = e.target.getAttribute('data-p')
      } else {
        linkVal = inputVal
        p = this.pvalue
      }
      let zhixia = [
        {
          'id': '110000',
          'name': '北京市'
        },
        {
          'id': '310000',
          'name': '上海市'
        },
        {
          'id': '500000',
          'name': '重庆市'
        },
        {
          'id': '120000',
          'name': '天津市'
        }]
      const zhixiaIndx = zhixia.findIndex(city => city.name === this.pvalue)
      if (zhixiaIndx >= 0) {
        linkVal = zhixia.filter(city => city.name === this.pvalue)[0].name
      }
      const indx = this.recentlyVisit.findIndex(recently => recently === linkVal)
      if (indx >= 0) this.recentlyVisit.splice(indx, 1)
      this.recentlyVisit.unshift(linkVal)
      this.recentlyVisit = this.recentlyVisit.length > 10 ? this.recentlyVisit.slice(0, 10) : this.recentlyVisit
      this.toggleCity(linkVal, p)
      this.$router.push('/')
    },
    toggleCity (c, p) {
      this.$store.dispatch({
        type: 'geo/toggleGeo',
        c,
        p
      })
    },
    async remoteMethod (queryStr) {
      this.qeuryList = []
      queryStr = queryStr.replace(/^\s*([a-zA-Z一-龢]*)\s*$/, '$1')
      if (queryStr === '') return
      this.qeuryList = this.allCities.filter(city => {
        if (city.name.indexOf(queryStr) >= 0) {
          return city
        }
      })
    },
    formatCitiedByLetter () {
      let o = {}
      let a = []
      this.allCities.map(city => {
        const firstLetter = pinyin4js.convertToPinyinString(city.name, '', pinyin4js.FIRST_LETTER).charAt(0).toUpperCase()
        if (!firstLetter.charCodeAt(0) > 65 && firstLetter.charCodeAt(0) < 90) return
        if (!o[firstLetter]) {
          o[firstLetter] = []
        }
        o[firstLetter].push({
          title: firstLetter,
          province: city.province,
          name: city.name
        })
      })
      const aCharCode = 'A'.charCodeAt(0)
      a = new Array(26)
      for (let [k, v] of Object.entries(o)) {
        const dirCharCode = k.charCodeAt(0) - aCharCode
        a[dirCharCode] = v
      }
      this.formatCitiedAboutLetter = a.filter(city => a !== '')
    },
    async getCities (p) {
      this.cvalue = ''
      const allPResult = await this.$http.get('http://cp-tools.cn/geo/province')
      const { province } = allPResult.data
      const [{ id: pid }] = province.filter(provinceItem => provinceItem.name === p)
      const res = await this.$http.get(`http://cp-tools.cn/geo/province/${pid}`)
      const { code, city } = res.data
      if (code === 0) {
        this.cities = city
      }
    }
  }
}
</script>
<style scoped>
  .container {
    margin: 30px 5% 0px 5%;
    padding: 20px 15px;
    border: 1px solid rgba(153, 153, 153, 0.3);
  }

  .choose_btn_wrapper {
    padding: 0 15px;
  }

  .provins_wrapper {
    width: 300px;
    position: relative;
  }

  .title {
    margin-right: 15px;
    font-size: 20px;
  }

  .input {
    margin-right: 15px;
  }

  .province_item {
    display: inline-block;
  }

  .clearfix::after {
    content: '';
    display: inline-block;
    clear: both;
    height: 0px;
    line-height: 0px;
    visibility: hidden;
  }

  .visited_wrapper {
    padding: 20px 20px 0px 15px;
    font-size: 20px;
  }

  .recently_title {
    font-size: 20px;
    display: inline-block;
  }

  .recently_item,
  .recently_list {
    display: inline-block;
  }

  .recently_item a {
    padding: 10px;
    font-size: 18px;
    white-space: nowrap;
  }

  .letter_wrapper {
    padding: 15px;
  }

  .letter_title {
    display: inline-block;
    font-size: 15px;
  }

  .letter_item {
    padding: 8px 10px;
    display: inline-block;
  }

  .letter_item a {
    display: block;
    padding: 1px;
    height: 20px;
    width: 20px;
    font-size: 18px;
    line-height: 20px;
    text-align: center;
  }

  .letter_item a:hover {
    border-radius: 50%;
    background-color: rgba(163, 162, 162, 0.3);
  }

  .fomat_city_wrapper {
    position: relative;
    padding: 20px 20px 20px 50px;
  }

  .fomat_city_wrapper:hover {
    background: rgba(153, 153, 153, 0.1);
  }

  .city_title {
    position: absolute;
    left: 10px;
    text-align: center;
    height: 40px;
    width: 40px;
    line-height: 40px;
    font-size: 20px;
    font-weight: 400;
    border-radius: 50%;
    background-color: #FFD000;
  }

  .city_item {
    display: inline-block;
  }

  .city_item a {
    display: block;
    padding: 10px 10px;
    white-space: nowrap;
    font-size: 15px;
    color: #222;
  }

  .city_item a:hover {
    color: rgb(100, 237, 141);
  }
</style>



