<template>
 <el-breadcrumb separator-class="el-icon-arrow-right">
   <el-breadcrumb-item :to="{ path: '/' }">{{city}}美团</el-breadcrumb-item>
   <el-breadcrumb-item :to="{path: '/product/'+path}" v-if="type">{{type}}</el-breadcrumb-item>
   <el-breadcrumb-item>{{product}}</el-breadcrumb-item>
 </el-breadcrumb>
</template>
<script>
export default {
  props: {
    city: {
      type: String
    },
    product: {
      type: String
    },
    path: {
      type: String
    },
    type: {
      type: String,
      default: ''
    }
  }
}
</script>



