<template>
  <div>
    <el-row>
      <el-col :span="14">
        <div class="block">
          <el-carousel trigger="click" height="250px">
            <el-carousel-item v-for="item in carousels" :key="item">
              <img :src="item" alt="" height="250px" width="100%">
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-col>
      <el-col :span="4" class="life">
        <img :src="life" alt="" width="100%" height="250px">
      </el-col>
      <el-col :span="6">
        <div class="right_user_wrapper">
          <div class="auser_login_register">
            <div class="avatar">
              <img :src="avatar" class="avatar" alt="">
            </div>
            <h5 class="hello">Hi!你好</h5>
            <div class="login">注册</div>
            <div class="register">登录</div>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="7">
        <div class="hotel"></div>
      </el-col>
      <el-col :span="7">
        <div class="funny"></div>
      </el-col>
      <el-col :span="4">
        <div class="store"></div>
      </el-col>
      <el-col :span="6" class="QR">
        <div class="QR_container">
          <div class="QR_code_wrapper">
            <img :src="downloadQr" alt="" height="80px">
            <h3 class="text">美团APP手机版</h3>
            <h5 class="introduce">
              <span>1元起</span><span>吃喝玩乐</span>
            </h5>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import carousel1 from './1.jpg'
import carousel2 from './2.jpg'
import carousel3 from './3.jpg'
import carousel4 from './4.png'
import life from './5.jpg'
import avatar from './avatar.jpg'
import downloadQr from './download-qr.png'
export default {
  data () {
    return {
      carousels: [carousel1, carousel2, carousel3, carousel4],
      life,
      avatar,
      downloadQr
    }
  }
}
</script>
<style scoped>
  .block {
    padding: 10px 0px 0px 10px;
  }

  .life {
    padding-left: 10px;
    padding-top: 10px;
    font-size: 0px;
  }

  .right_user_wrapper {
    margin: 10px 10px 0 10px;
    box-sizing: border-box;
    height: 250px;
    background-color: #fff;
  }

  .auser_login_register {
    margin: 0 auto;
    height: 250px;
    width: 50%;
    text-align: center;
    background-color: #fff;
  }

  .avatar {
    padding-top: 10px;
    border-radius: 50%;
    display: inline-block;
    width: 60px;
    height: 60px;
  }

  .hello {
    padding-top: 20px;
    font-size: 14px;
  }

  .login,
  .register {
    display: inline-block;
    margin-top: 15px;
    text-align: center;
    height: 40px;
    line-height: 40px;
    width: 70%;
    border: 1px solid #555;
    border-radius: 20px;
    cursor: pointer;
  }

  .hotel,
  .funny,
  .store {
    margin: 10px 0 0 10px;
    height: 166px;
  }

  .hotel {
    background: url('./hotel.png') center center;
    background-size: 100% 166px;
  }

  .funny {
    background: url('./funny.jpg') center center;
    background-size: 100% 166px;
  }

  .store {
    background: url('./store.jpg') center center;
    background-size: 100% 166px;
  }

  .QR {
    height: 166px;
  }

  .QR_container {
    margin: 10px;
    height: 100%;
    ;
    background-color: #fff;
  }

  .QR_code_wrapper {
    padding-top: 20px;
    text-align: center;
  }

  .text {
    padding-top: 15px;
    font-size: 14px;
  }

  .introduce {
    padding-top: 10px;
  }

  .introduce span:first-child {
    color: red;
    margin-right: 8px;
  }
</style>



