<template>
  <el-row>
    <el-col :span="24">
      <dl class="store">
        <span class="order">{{order}}</span>
        <dt class="img_wrapper" @click="linkToDetail(store.type, store.title)">
          <img  :src="store.url" class="image" @load="load">
        </dt>
        <dd>
          <div class="store_infos">
            <h5 class="store_title">{{store.title}}</h5>
            <div class="star_wrapper">
              <star :value="Number(store.rating)" class="star"></star>
              <span class="score">{{store.rating}}分</span>
              <span class="comment_count">{{store.commentCount}}条评论</span>
            </div>
            <div class="adr_wrapper">
              <span class="">{{store.type}}&nbsp;|&nbsp;</span>
              <span class="address">{{store.address}}</span><div class="map_btn" @click="showMap"><img class="imgIcon" :src="mapImg" width="20" alt=""><span>查看地图</span></div>
            </div>
            <div class="avarage_price_wrapper">
              <span class="avarage_price" v-if="!isNaN(Number(store.cost))">人均{{store.cost}}元</span>
              <span class="avarage_price" v-else>{{store.cost}}</span>
            </div>
            <!-- <div class="ticket_wrapper">
              <h5 class="store_title">{动物园儿童票}</h5>
              <div class="price_wrapper">
                <span class="current_price">￥74元</span>
                <span class="old_price">门市价￥76元</span>
                <span class="sell_count">已售100</span>
              </div>
            </div> -->
          </div>
        </dd>
      </dl>
    </el-col>
  </el-row>
</template>
<script>
import star from '@/base/star/star'
import {mapState} from 'vuex'
export default {
  components: {
    star
  },
  props: {
    store: {
      type: Object,
      default: () => {}
    },
    order: {
      type: Number
    }
  },
  data () {
    return {
      mapImg: require('./map.png')
    }
  },
  computed: {
    ...mapState({
      currentGeo: state => state.geo.currentGeo.name
    })
  },
  mounted () {
    this.$emit('ready')
  },
  methods: {
    load () {
      this.$emit('imgload')
    },
    showMap () {
      this.$emit('show-map')
    },
    async linkToDetail (type, title) {
      const city = this.currentGeo
      const keyword = title
      this.$router.push({path: '/detail', query: {city, keyword, type}})
    }
  }
}
</script>
<style scoped>
  .store {
    position: relative;
    padding: 15px 0px 15px 220px;
    /* border-bottom: 1px solid rgba(102, 102, 102, 0.3); */
  }
  .order {
    position: absolute;
    z-index: 10;
    left: -12px;
    top: 25px;
    height: 10px;
    width: 25px;
    padding: 3px 0px;
    background-color: #FB6433;
    border-radius: 4px;
    color: #fff;
    text-align: center;
  }
  .img_wrapper {
    position: absolute;
    left: 0px;
    width: 220px;
    height: 120px;
    overflow: hidden;
  }
  .image {
    width: 100%;
    border-radius: 4px;
  }
  .store_infos {
    padding-left: 15px;
  }
  .avarage_price_wrapper {
    padding-bottom: 10px;
    border-bottom: 1px dashed rgba(102, 102, 102, 0.5);
  }
  .star_wrapper, .adr_wrapper, .avarage_price_wrapper, .ticket_wrapper, .price_wrapper {
    padding-top: 15px;
  }
  .star {
    display: inline-block;
    vertical-align: middle;
  }
  .score {
    padding: 0px 7px;
  }
  .comment_count {
    color: #F90;
  }
  .current_price {
    margin-right: 8px;
    margin-left: -2px;
    color: #F90;
    font-size: 16px;
    font-weight: 400;
  }
  .old_price {
    margin-right: 8px;
  }
  .imgIcon {
    vertical-align: middle;
    margin: -5px 10px 0px 0px;
  }
  .map_btn {
    position: absolute;
    right: 50px;
    cursor: pointer;
  }
</style>



