<template>
  <el-row>
    <el-col :span="24">
      <el-col :span="24" class="star">
        <el-rate
          v-model="value"
          disabled
          text-color="#ff9900"
          :colors="colors"
          :show-text="true"
          score-template="{value}">
        </el-rate>
        </el-col>
    </el-col>
  </el-row>
</template>
<script>
export default {
  props: {
    value: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      colors: ['#99A9BF', '#F7BA2A', '#FF9900']
    }
  }
}
</script>



