<template>
  <el-row class="footer_wrapper">
    <el-col :span="24">
      <div class="topbar">
        <span class="text">美团外卖</span>
      </div>
    </el-col>
    <el-col :span="2">
      <h5 class="title">热门城市</h5>
    </el-col>
    <el-col :span="22">
      <ul class="list">
        <li class="list_item" :key="indx" v-for="(item, indx) in hotCities">
          <router-link class="link" to="/" >{{item}}</router-link>
        </li>
      </ul>
    </el-col>
    <el-col :span="2">
      <h5 class="title">热门分类</h5>
    </el-col>
    <el-col :span="22">
      <ul class="list">
        <li class="list_item" :key="indx" v-for="(item, indx) in categories">
          <router-link class="link" to="/" >{{item}}</router-link>
        </li>
      </ul>
    </el-col>
    <el-col :span="2">
        <h5 class="title">周边热门</h5>
      </el-col>
      <el-col :span="22">
        <ul class="list last_list" >
          <li class="list_item" :key="indx" v-for="(item, indx) in surroundHot">
            <router-link class="link" to="/" >{{item}}</router-link>
          </li>
        </ul>
      </el-col>
  </el-row>
</template>
<script>
import {hotCities, categories, surroundHot} from '@/public/js/footerTopBar'
export default {
  data () {
    return {
      hotCities,
      categories,
      surroundHot
    }
  }
}
</script>
<style scoped>
.footer_wrapper {
  margin: 50px 5%;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  border: 1px solid rgba(153, 153, 153, 0.3);
  border-top: none;
}
.topbar {
  width: 100%;
  padding: 0 1px;
  margin-left: -1px;
  height: 50px;
  line-height: 50px;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  background-color: #0ECAC7;
  color: #fff;
  font-size: 16px;
}
.text {
  margin-left: 10px;
  font-size: 16px;
  color: #fff;
  font-weight: 500;
}
.title {
  padding-top: 20px;
  font-size: 16px;
}
.list {
  /* overflow: hidden; */
  position: relative;
  border-bottom: 1px solid rgba(153, 153, 153, 0.3);
  padding: 10px 30px;
  font-size: 0px;
}
.last_list {
  border: none;
}
.list_item {
  /* float: left; */
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 10%;
}
.link {
  display: inline-block;
  padding: 10px 0px;
}
</style>



